loadData(){
  this.api.getScorecardQuestions(this.accountType).subscribe((response) => {  
    console.log(response);
    for (let n = 0; n < 1; n++) {
        this.scoreboardQuestionsArray.push({scorecardQuestion: response,applicantId: n + 1, scoreCardDetails: response});
        let mapDataAns: any[] = [];
        console.log("data",this.scoreCardDetails);
        for (let p = 0; p < response.length; p++){
          mapDataAns.push({scoreCardQuestionId: response[p].scoreCardQuestionId, scorecardAnswer: null, scoreCardMappingId: null}); 
        }
        this.scoreCardMappingArray.push({applicantId: n + 1, scoreCardDetails: mapDataAns});
    }

    console.log("data",this.scoreCardDetails);
    console.log("ques:",this.scoreboardQuestionsArray)
    this.cdr.markForCheck();
  });
}

