  _keyPress1(event:any){
    const pattern = /[a-zA-Z]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  saveReports() {
    let submitData: any = {};
    console.log(submitData);
    console.log(this.no_of_applicants);
    let numberOfApplicant = this.no_of_applicants;
    submitData.accountId = this.accountId;
    submitData.accountType = "savings";
    submitData.numberOfApplicant = numberOfApplicant;
    // console.log(this.scoreCardId.value);
    submitData.scoreCardId = this.scoreCardId.value;
    // submitData.loggedInUser = this.loggedInUser.id;
    submitData.loggedInUser = 123;//This value is temporary and logged In User id has to be passed.
    // console.log(this.scoreCardMappingArray);
    submitData.scoreCardMapping = this.scoreCardMappingArray;
    console.log(submitData);

    this.api.saveQualitativeScorecard(submitData).subscribe((resp) => {
      this.snack.open('Qualitative Scoreboard Saved!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
      // console.log(resp);
      this.isDataSaved = true;
      this.ls.setItem('ASSESSMENT_QA_DONE', true);
      // this.disableDoneBtn=false
      this.accountService.refresh.next();
      if(this.overDraftByStatus == true){
      this.router.navigate([`/home/application/assessment/report/${this.accountId}`]);
      }
      else{
        let fundBy = this.ls.getItem('fundByStatus');
        if(fundBy.isActive == true){
          this.router.navigate([`/home/application/funding/list/${this.accountId}`]);
          }
          else{
            this.router.navigate([`/home/application/approval/list/${this.accountId}`]);
          }

      }
    }, err => {
      this.snack.open('Server Error!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    });
  }

  done() {
    this.ls.setItem('ASSESSMENT_QA_DONE', true);
    this.accountService.refresh.next();
    this.router.navigate([`/home/application/assessment/report/${this.accountId}`]);
  }
