import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { FundingStageService } from 'app/shared/services/funding-stage.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { DialogData } from 'app/views/others/kyc-screening/kyc-screening.component';

@Component({
  selector: 'app-intial-funding-dialog',
  templateUrl: './intial-funding-dialog.component.html',
  styleUrls: ['./intial-funding-dialog.component.scss']
})
export class IntialFundingDialogComponent implements OnInit {
  fundStageFormGroup: FormGroup;
  detailsShowOrViewRef: any;
  accountId: string;
  accountInfo: any;
  constructor(private router: Router,
    private formBuilder: FormBuilder,
    private cdr: ChangeDetectorRef,
    private ls: LocalStoreService,
    private loader:AppLoaderService,
    private fundingStageService: FundingStageService,
    public dialogRef: MatDialogRef<IntialFundingDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData,
    private snack: MatSnackBar) { }

  ngOnInit() {
    /* For getting account id */
    this.accountId = this.ls.getItem('accountId');
    this.accountInfo = this.ls.getItem('EDIT-CREATEACCCIF');
    if(this.accountInfo){
      this.accountId=this.accountInfo.businessProductAccount.accountId
    }
    this.fundStageFormGroup = this.formBuilder.group({
      "fundId": [''],
      "fundBy": [''],
      "accountId": [''],
      "amount": [''],
      "tellerTrxnRefNo": [''],
      "tellerTrxnStatus": [''],
      "fundByAccount": [''],
      "chequeNumber":[''],
      "branchCode":[''],
      "createdBy": [''],
      "createdTime": [''],
      "modifiedBy": [''],
      "modifiedTime": ['']
    })
     
setTimeout(() => {
  this.viewFundsbyAccountId(this.accountId);
}, 1);
    
  }

  goToDetail(item) {
    console.log(item);
      this.fundingStageService.addEditOrViewsummaryData('EDIT');
      console.log(this.fundingStageService.addEditOrViewsummaryData)
      this.fundingStageService.fundsEditResp(item);
      this.router.navigate([`/home/application/funding/details/${this.accountId}`]);
      this.dialogRef.close();
  }

  viewFundsbyAccountId(accountId){
    this.loader.open();
    this.fundingStageService.getFundByAccountId(accountId)
    .subscribe((fundResp:any) =>{
      this.loader.close();
      this.fundStageFormGroup.patchValue(fundResp);
      this.cdr.markForCheck();
    },(err:any) =>{
      this.loader.close();
    });
  }

  goToClose(): void {
    this.dialogRef.close();
  }

}
