import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import * as moment from 'moment';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from '../../account.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { DataService } from 'app/shared/services/data.service';
import { empty, Subscription } from 'rxjs';
import { Year } from '@syncfusion/ej2-schedule';
import Swal from 'sweetalert2';
import { UrlService } from '../../url.service';
import { minDate } from 'ngx-custom-validators/src/app/min-date/validator';


@Component({
  selector: 'app-unsecured-draft',
  templateUrl: './unsecured-draft.component.html',
  styleUrls: ['./unsecured-draft.component.scss']
})
export class UnsecuredDraftComponent implements OnInit {
  unsecuredForms: FormGroup;
  submitted = false;
  accountId: any;
  months: any;
  currency: any;
  selectedYear: number;
  years: number[] = [];
  selectedDay: number = 1;
  days: number[] = [];

  tomorrow = new Date();
  maxDate = new Date();
  minDate = new Date();

  unsecureoverdraftdata: any;

  navSubscription: Subscription;
  routeparams: any;
  showEditOption: boolean = false;
  loggedInUser: any = "123456";
  previousUrl: string = '';
flag:boolean;
  isSecuredScreen: boolean;
  isUnSecureDraftScreen: any;
  constructor(private api: ApiService, private snack: MatSnackBar,
    private formBuilder: FormBuilder, private router: Router,
    private cdr: ChangeDetectorRef,
    private ls: LocalStoreService, private accountService: AccountService,
    private dataService: DataService, private urlService: UrlService) {

    this.selectedYear = new Date().getFullYear();
    for (let year = this.selectedYear; year >= 2000; year--) {
      this.years.push(year);
    }

    // this.selectedDay = new Date().getDay();
    for (let day = this.selectedDay; day <= 31; day++) {
      this.days.push(day);
    }

    this.tomorrow.setDate(this.tomorrow.getDate());
  }

  ngOnInit() {
    this.accountId = this.ls.getItem('accountId');
    console.log( this.ls.getItem('LIMIT_SECURED_DONE'));
    this.isSecuredScreen = this.ls.getItem('LIMIT_SECURED_DONE');
    console.log(this.isSecuredScreen);
    if(this.isSecuredScreen!=null|| this.isSecuredScreen==true){
      this.snack.open('Secured Overdraft Is Already Filled,so click next button to continue!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    }
    this.isUnSecureDraftScreen = JSON.parse(this.ls.getItem('LIMIT_UNSECURED_DONE'));

    this.months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
    // this.currency = ['USD', 'EUR', 'INR'];
    this.getCurrencyDetails()

    this.urlService.previousUrl$
      .subscribe((previousUrl: string) => {
        this.previousUrl = previousUrl
      });
console.log(this.isUnSecureDraftScreen);
    if (this.accountId != null && this.isUnSecureDraftScreen) {
      this.showEditOption = true;
      this.getUnsecuredOverdraftSummary();
    } else {
      this.showEditOption = false;
      this.buildUnsecuredForm();
    }
  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }
  

  getUnsecuredOverdraftSummary() {
    this.api.UnsecuredOverdraftSummary(this.accountId)
      .subscribe((result: any) => {
        console.log(result);
        if(result.renewPeriod || result.renewalAmount){
          this.flag=true;
        }
        this.buildUnsecuredForm(result, true);
      })
  }

  buildUnsecuredForm(data?, isResponse?: boolean) {
    this.unsecuredForms = this.formBuilder.group({
      limitId: [data ? data.limitId : '', Validators.required],
      accountId: this.accountId != null ? this.accountId : '',
      overdraftCurrency: [data ? data.overdraftCurrency : '', Validators.required],
      overdraftLimitAmount: [data ? data.overdraftLimitAmount : '', Validators.required],
      limitStartDate: [data ? new Date(data.limitStartDate) : '',Validators.required],
      limitEndDate: [data ? new Date(data.limitEndDate) : '',Validators.required],
      unsecuredLimit: [''],
      renewPeriod: [data ? data.renewPeriod : ''],
      renewalAmount: [data ? data.renewalAmount : ''],
      year: [data ? data.year : ''],
      month: [data ? data.month : ''],
      day: [data ? data.day : ''],
      overdraftLimitRenewed: [data ? data.overdraftLimitRenewed : ''],
    });

    if (isResponse) {
      console.log("values:", data.renewPeriod);
      if (data.renewPeriod) {
        this.selectedYear = data.renewPeriod.substring(0, 4);
        let selectedMonth = data.renewPeriod.substring(5, 8);
        console.log(selectedMonth)
        this.selectedDay = data.renewPeriod.substring(9, 11);
        console.log(this.selectedDay);

        this.unsecuredForms.get('year').setValue(Number(this.selectedYear));
        this.unsecuredForms.get('month').setValue(selectedMonth);
        this.unsecuredForms.get('day').setValue(Number(this.selectedDay));
        this.cdr.markForCheck();
      }
    }
  }


  onToggle(event) {
    if(event.checked=== true){
    this.flag=true;
    }
    else{
      this.flag=false;
      this.unsecuredForms.get('renewalAmount').setValue('');
    }
    console.log("on toggle value", event.checked)
  }


  onSubmit(form) {
    if(this.unsecuredForms.value.overdraftLimitRenewed){
      if(this.unsecuredForms.controls.day.value==null ||this.unsecuredForms.controls.year.value==null || this.unsecuredForms.controls.month.value==null){
        this.snack.open('Please give Renew Period', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        return;
      }
    }
    console.log("form values", form)
    this.submitted = true;

    let startDate = this.convertDate(this.unsecuredForms.controls.limitStartDate.value);
    this.unsecuredForms.value['limitStartDate'] = startDate;
    let endDate = this.convertDate(this.unsecuredForms.controls.limitEndDate.value);
    this.unsecuredForms.value['limitEndDate'] = endDate;
    let renewData;

    if (this.unsecuredForms.value.overdraftLimitRenewed) {
      renewData = this.unsecuredForms.value.year + '-' + this.unsecuredForms.value.month + '-' + this.unsecuredForms.value.day;
    } else {
      renewData = '';
    
    }

    let limitData = this.unsecuredForms.value.limitId;
    let currencyData = this.unsecuredForms.value.overdraftCurrency;
    let overdraftLAData = this.unsecuredForms.value.overdraftLimitAmount;
    let limitStartDateData = this.unsecuredForms.value.limitStartDate;
    let limitEndDateData = this.unsecuredForms.value.limitEndDate;
    let overdraftLimitRenewed = this.unsecuredForms.value.overdraftLimitRenewed;
    let renewalAmountData = this.unsecuredForms.value.renewalAmount;
    let finalPayload = {
      accountId: this.accountId,
      limitId: limitData,
      overdraftCurrency: currencyData,
      overdraftLimitAmount: overdraftLAData,
      limitStartDate: limitStartDateData,
      limitEndDate: limitEndDateData,
      renewPeriod: renewData,
      renewalAmount: renewalAmountData,
      overdraftLimitRenewed: overdraftLimitRenewed
    };
    console.log("finalPayload", finalPayload);

    console.log('SUCCESS!! :-)\n\n', this.unsecuredForms.value);
  
    console.log(this.isSecuredScreen)
    // if(this.showEditOption){
    // for update put
    //  let payload = {UnsecuredOverdraftdeatils:finalPayload, loggedInUser:this.loggedInUser};
    // console.log(payload);
    if (finalPayload.limitId == null || finalPayload.overdraftLimitAmount == null) {
      Swal.fire('Please Enter The Mandatory Fields !');
      return false;
    }
    
    // if ((finalPayload.renewPeriod == "" || finalPayload.renewalAmount == "") && this.flag==true) {
    //   Swal.fire('Please Enter The Renewed Fields !');
    //   return false;
    // }
 
    // if((this.isSecuredScreen==false  || this.isSecuredScreen==null) || (this.isUnSecureDraftScreen==false && this.isSecuredScreen==true && this.unsecuredForms.value!=null) ){
 
    if(this.unsecuredForms.value.limitId!=''){
    this.api.unsecuredDraft(finalPayload).subscribe(resp => {

      console.log("resp after saving basic info", resp)
      this.snack.open('UnSecured Overdraft Details Saved!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
      this.ls.setItem('LIMIT_UNSECURED_DONE', true);
      this.accountService.refresh.next();
      this.router.navigate([`/home/application/limit-entry-stage/financial-details/${this.accountId}`]);

    }, err => {
      this.snack.open(err['message'], 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
      this.ls.setItem('LIMIT_UNSECURED_DONE', true);
      this.accountService.refresh.next();
      this.router.navigate([`/home/application/limit-entry-stage/financial-details/${this.accountId}`]);
    })
  }
  // if(this.isUnSecureDraftScreen==false){
    else{
    console.log("Else partt");
    this.ls.setItem('LIMIT_UNSECURED_DONE', false);
    this.accountService.refresh.next();
    this.router.navigate([`/home/application/limit-entry-stage/financial-details/${this.accountId}`]);
  }
    // }
    // else{
    // }

  }

  convertDate(value) {
    let date = moment(value).format('YYYY-MMM-DD');
    // let date = moment(value).format('YYYY-MM-DDThh:mm:ss');
    console.log("date", date);
    if (date)
      return date;
  }

  goBack() {
    this.router.navigate([this.previousUrl]);
  }

  onKeyPress(event: any) {
    const number = /[0-9\+\-\ ]/;
    let inputCharacter = String.fromCharCode(event.charCode);
    if (event.keyCode != 8 && !number.test(inputCharacter)) {
      event.preventDefault();
      console.log("event:", event)
    }
    console.log(this.unsecuredForms.controls['limitId'])
  }
  reset() {
    this.unsecuredForms.reset();
  }

  ngOnDestroy() {
    this.dataService.sendNavParam({});
  }
  getCurrencyDetails(){
  this.api.fetchCurrencyMaintAuthRecs().subscribe(resp => {
  this.currency = resp    
  }) 
}

}
