import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder, FormArray } from '@angular/forms';
import { Router } from '@angular/router';
import { CustomValidators } from 'ngx-custom-validators';
import { MatSnackBar } from '@angular/material/snack-bar';
// import { IcustProductAccountInfo } from 'app/shared/models/icustProductAccountInfo';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';
import { ApiService } from 'app/shared/services/api.service';
import { AccountService } from '../../account.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { DataService } from 'app/shared/services/data.service';
import * as moment from 'moment';
import { UrlService } from '../../url.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';


@Component({
  selector: 'app-account',
  templateUrl: './account.component.html',
  styleUrls: ['./account.component.scss']
})
export class AccountComponent implements OnInit {
  cifNumberForm = new FormControl()
  timer: any;
  listOfCustByCif: any = [];

  formData = {}
  // console = console;
  accountForm: FormGroup;
  isAccountActive: boolean = false;
  navSubscription: any;
  routeparams: any;
  showEditOption: boolean = false;
  productaccountID: any;
  customerDetails: any[] = [];
  minDate = new Date();
  previousUrl: string = '';
  accountDetails: any;
  accountFromByCif: FormGroup;
  accountDetailsData: any;
  idProof: any;
  showSpinn: boolean;
  userAdddress: any;
  customerAdditionalInfo: any;
  accountId: any;
  dispResponse: {};
  cifNumberPrimary: any;
  isCifValid: boolean = false;
  onbackSpace: boolean = false;
  arrayAccountBranch: any;
  currencyData: any;
  branchName: any;
  arrayAccountType: any;
  userName: string;
  disableFundTheAccount: boolean = false;
  fundBy: string;
  flag: boolean;
  swiftCode: any;
  customerName: any;
  name: string;

  validPattern: string;
  maxDate = new Date();
  nextbtn: boolean = false;
  customerNumber: any;
  accountInfo: any;



  constructor(private api: ApiService, private router: Router, private cdr: ChangeDetectorRef, private dataService: DataService, private formBuilder: FormBuilder, private snack: MatSnackBar,
    private apiService: ApplicationEntryStageService, private urlService: UrlService, private ls: LocalStoreService, private accountService: AccountService, private loader: AppLoaderService,
    private appApiService: ApplicationEntryStageService,
  ) {
    this.maxDate.setDate(this.maxDate.getDate() + 13);
  }


  ngOnInit() {

    this.userName = localStorage.getItem('userName');
    this.productaccountID = this.ls.getItem('accountId');
    this.accountInfo = this.ls.getItem('EDIT-CREATEACCCIF');
    console.log(this.accountInfo);
    let isAccountScreen: boolean = JSON.parse(this.ls.getItem('ACC_DONE'));
    this.getAccountType("account_type");
    this.validPattern = "^[a-zA-Z0-9]{8}$";
    this.urlService.previousUrl$
      .subscribe((previousUrl: string) => {
        this.previousUrl = previousUrl
      });

    console.log(this.showEditOption);

    if (this.productaccountID != null && isAccountScreen) {
      this.showEditOption = true;
      this.cifNumberPrimary = this.ls.getItem("CIF_NUM_PRIMARY");
      this.isCifValid = true;
      this.getAccountDetails();
    } else {
      console.log("else");
      this.showEditOption = false;
      this.buildAccountForm();
    }
    this.getAccountBranchDetails();
    if (this.accountForm && this.accountForm.get('accountBranch')) {
      this.accountForm.get('accountBranch').setValue(this.branchName);
    }
    this.onChanges();
    console.log("account branch::", this.branchName)
    if(this.accountInfo){
      console.log("Inside Fetch");
      this.fetchByCif(this.accountInfo.businessProductAccount.customerId);
    }
  }

  onKeyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
    console.log(event.target.value);
    clearTimeout(this.timer)
    if (!event.target.value) {
      this.listOfCustByCif = []
    } else {
      this.timer = setTimeout(() => {
        this.appApiService.fetchByCif(event.target.value).subscribe((res: any) => { 
          console.log(res);
          if (res) {
            this.listOfCustByCif = res.customerInfoList
          }
        })
      }, 1000);
    }
  }


  onChanges(): void {
    this.accountForm.get('cifNumber').valueChanges.subscribe(val => {
      if (val == null) {
        this.isCifValid = false;
      }
      console.log("TEST CIF", `My cifNumber is ${val}.`);
    });

    this.accountForm.get('fundBy').valueChanges.subscribe(val => {
      if (val == 'yes') {
        this.disableFundTheAccount = true;

      } else {
        this.disableFundTheAccount = false;

      }

    });
  }


  get f() { return this.accountForm.controls; }


  getAccountDetails() {
    this.loader.open();
    this.api.getAccountDetailsById(this.productaccountID).subscribe((accountDetails) => {
      this.loader.close();
      this.accountDetails = accountDetails;
      console.log(this.accountDetails);
      this.buildAccountForm(this.accountDetails);
    }, (err: any) => {
      this.loader.close();
    });


  }

  buildAccountForm(data?) {
    console.log(data);
    let applicationDate = data ? moment(data.applicationDate).format() : '';
    this.flag = (data && data.swiftCodeEnable) ? true : false;
    if (data) {
      if (data.fundTheAccount != null) {
        console.log(this.disableFundTheAccount, "If");
        this.disableFundTheAccount = true;
        console.log(this.disableFundTheAccount, "If");
      }
    }

    this.accountForm = this.formBuilder.group({
      cifNumber: [data ? this.cifNumberPrimary : '', [Validators.required, Validators.minLength(5)]],
      customerName: [data ? this.ls.getItem('NAME') : ''],
      accountId: [this.productaccountID],
      accountType: [data ? data.accountType : '', Validators.required],
      businessProductName: [data ? data.businessProductName : '', Validators.required],
      productImage: [data ? data.productImage : ''],
      productDescription: [data ? data.productDescription : '', Validators.required],
      accountBranch: [data ? data.accountBranch : '', Validators.required],
      accountCurrency: [data ? data.accountCurrency : '', Validators.required],
      applicationDate: [data ? applicationDate : '', Validators.required],
      fundTheAccount: [data ? data.fundTheAccount : '0'],
      fundBy: [data ? data.fundBy : false],
      amount: [data ? data.amount : '0'],
      overDraftRequested: [data ? data.overDraftRequested : false],
      status: [data ? data.status : ''],
      swiftCodeEnable: [data ? data.swiftCodeEnable : false],
      swiftCode: [data ? data.swiftCode : '', Validators.pattern("(?=.*[0-9])(?=.*[a-zA-Z])([a-zA-Z0-9]+){8,8}$")],
    });

  }

  //SWIFT Code
  onToggle(event: any) {
    if (event.checked == true) {
      this.nextbtn = true;
      //this.accountForm.get('swiftCode').disable({onlySelf:true, emitEvent:false});
      this.flag = true;

      // this.accountForm.get('swiftCode').setValue('');
    }
    else {
      this.nextbtn = false;
      this.flag = false;
      this.accountForm.controls.swiftCode.reset();
      this.cdr.detectChanges();
      this.cdr.markForCheck();
    }
    console.log("acc", this.accountForm)
    console.log("accountForm", this.accountForm.valid, this.nextbtn)
  }


  goBack() {
    this.router.navigate([this.previousUrl]);
  }

  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

  _keyPress1(event: any) {
    const pattern = /[a-zA-Z0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
    if (this.accountForm.get('swiftCode')) {
      this.nextbtn = false;
    }
    else {
      this.nextbtn = true;
    }
    console.log("value", this.accountForm.value.swiftCode)
    console.log("acc", this.accountForm.invalid)
    console.log("nxt", this.nextbtn)

  }

  keyUpevent(event) {
    console.log("event", event);
    if (event.keyCode === 8) {
      this.onbackSpace = true;
      this.isCifValid = false;
    }
    else {
      this.onbackSpace = false;
    }
  }


  getAccountType(key: string) {
    this.api.getDropDownValues(key).subscribe((data) => {
      this.arrayAccountType = data;
    });
  }

  getAccountBranchDetails() {
    //debugger
    this.api.getAccountBranch().subscribe((data) => {
      console.log("data:: AccountBranch ", data)
      this.arrayAccountBranch = data;
      if (this.arrayAccountBranch.length == 0) {
        this.arrayAccountBranch.push(this.branchName);
      }
      console.log("this.branchName::", this.branchName, this.arrayAccountBranch)

      this.currencyData = this.arrayAccountBranch.find(x => x.branchName == this.branchName);
      console.log("Currency", this.currencyData)

      if (this.currencyData != null || this.currencyData != undefined) {
        this.accountForm.get('accountCurrency').setValue(this.currencyData.branchCcy);
      }

    });
  }


  onChangeBranch(value) {
    this.currencyData = this.arrayAccountBranch.find(x => x.branchName == value.value);
    if (this.currencyData != null || this.currencyData != undefined) {
      this.accountForm.get('accountCurrency').setValue(this.currencyData.branchCcy);
    }

  }
  convertDate(value) {
    let date = moment(value).format('YYYY-MMM-DD'); // ISO 8601 format
    // let date = new Date(value).format('yyyy-MMM-dd');
    console.log("date", date);
    if (date)
      return date;
  }

  onSubmit() {
    console.log("form value", this.accountForm.value);
    localStorage.setItem('CurrencyType', this.accountForm.value.accountCurrency);
    console.log("fund::", this.accountForm.value.fundBy);
    let payload = {

      "accountId": this.showEditOption ? this.productaccountID : null,
      "accountType": this.accountForm.value.accountType,
      "businessProductName": this.accountForm.value.businessProductName,
      "productImage": this.accountForm.value.productImage,
      "productDescription": this.accountForm.value.productDescription,
      "accountBranch": this.accountForm.value.accountBranch,
      "accountCurrency": this.accountForm.value.accountCurrency,
      "applicationDate": this.convertDate(this.accountForm.value.applicationDate),
      "fundTheAccount": this.accountForm.value.fundTheAccount,

      "fundBy": this.accountForm.value.fundBy,
      "amount": this.accountForm.value.amount,
      "overDraftRequested": this.accountForm.value.overDraftRequested,
      "status": this.accountForm.value.status,
      "createdBy": this.userName,
      "swiftCode": this.accountForm.value.swiftCode,
      "swiftCodeEnable": this.accountForm.value.swiftCodeEnable,
      "customerName": this.ls.getItem('NAME'),
    };

    if (this.isCifValid == false) {
      this.snack.open('Kindly Enter Valid CifNumber to proceed!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
      return;
    }

    this.apiService.saveProductAccountInfo(payload).subscribe(response => {
      console.log("Response ---> ", response);
      console.log(response.accountId)
      this.accountId = response.accountId;
      this.accountForm.value.swiftCode;
      console.log("swiftCode:", this.accountForm.value.swiftCode)
      this.assign(response.accountId, response.accountType)
      if (response === 200 || response !== null) {
        sessionStorage.setItem('accountId', response.accountId);
        let fundStatus = {
          accountId: response.accountId,
          isActive: response.fundBy ? true : false
        };
        let overDraftStatus = {
          accountId: response.accountId,
          isActive: response.overDraftRequested
        };

        this.ls.setItem('fundByStatus', fundStatus);
        this.ls.setItem('fundByStatusAccId', fundStatus.accountId);
        console.log(fundStatus.isActive, "fundByStatus");
        this.ls.setItem('overDraftByStatus', overDraftStatus);
        console.log(overDraftStatus.isActive, "overDraftStatus");
        this.ls.setItem('accountId', response.accountId);
        this.ls.setItem('accountType', response.accountType);
        this.ls.setItem("CIF_NUM_PRIMARY", this.accountForm.value.cifNumber);
        this.ls.setItem('Customer Name', this.name);
        this.ls.setItem("SWIFTCode", this.accountForm.value.swiftCode);
        this.ls.setItem('ACC_DONE', true);
        this.ls.setItem('ENRICHMENT_INTEREST_DONE', false);
        this.ls.setItem('ENRICHMENT_CHARGES_DONE', false);
        this.router.navigate([`/home/application/customer-information/${response.accountId}`]);
        this.accountService.refresh.next();
        this.snack.open('Account Details Saved!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      }
      else if (response === 500) {
        this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        console.log("500 error")
      }
    });
    let cifnu = this.accountForm.get('cifNumber').value;
    //this.fetchByCif(cifnu);
  }


  assign(valueForaccId, type) {
    this.api.setAccountId({
      accountId: valueForaccId
    });
    this.api.setAccountType({
      accountType: type
    });
  }

  fetchByCif(cifNumber) {

    if (!!cifNumber) {
      this.showSpinn = true;
      console.log("Caling", cifNumber);
      this.apiService.fetchByCif(cifNumber).subscribe((res: any) => {
        console.log('resp', res);
        // this.accountForm.reset();
      console.log(this.isCifValid);
        if (res.customerInfoList[0].custStatus!="APPROVED")  
        {
          this.isCifValid = false;
            this.snack.open("CIF Number is not Approved :" + cifNumber, "", {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
  
        this.accountForm.controls.cifNumber.setValue(cifNumber);
        this.currencyData = this.arrayAccountBranch.find(x => x.branchName == this.branchName);
        this.accountForm.get('accountBranch').setValue(this.branchName);
        if (this.currencyData != null || this.currencyData != undefined) {
          this.accountForm.get('accountCurrency').setValue(this.currencyData.branchCcy);
        }
        this.accountForm.get('amount').setValue(0);
        this.accountForm.get('swiftCode').setValue(null);
        // console.log('currencyData:: ',this.currencyData);
        this.showSpinn = false;

        if (!res) {
          this.isCifValid = false;

          this.snack.open('Invalid CIF Number', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
          // this.accountForm.get('cifNumber').patchValue("");
          return;
        } else {
          if (res.customerInfoList[0].custStatus!="APPROVED") {
            this.isCifValid= false;
          }
          if (res.customerInfoList[0].custStatus=="APPROVED"){
            this.isCifValid = true;
          }
           //console.log(this.isCifValid,"else2 condition");

          let address: any;
          console.log(res.customerInfoList[0].userAddress.length);
          if (res.customerInfoList[0].userAddress.length > 1) {
            address = res.customerInfoList[0].userAddress.find((item: any) => item.addressType == "Permanent" || item.addressType == "permanent");
          } else {
            address = res.customerInfoList[0].userAddress[0];
          }
          let queryParams = {
            prefix: res.customerInfoList[0].prefix,
            customerId: res.customerInfoList[0].customerId,
            firstName: res.customerInfoList[0].firstName,
            middleName: res.customerInfoList[0].middleName,
            lastName: res.customerInfoList[0].lastName,
            gender: res.customerInfoList[0].gender,
            dateOfBirth: this.convertDate(res.customerInfoList[0].dateOfBirth),
            placeOfBirth: res.customerInfoList[0].placeOfBirth,
            residenceStatus: res.customerInfoList[0].residenceStatus,
            nationality: res.customerInfoList[0].nationality,
            countryOfResidence: res.customerInfoList[0].countryOfResidence,
            occupationType: res.customerInfoList[0].occupationType,
            custIdProofs: res.customerInfoList[0].custIdProofs,
            idType: res.customerInfoList[0].custIdProofs.length > 0 ? res.customerInfoList[0].custIdProofs[0].idType : '',
            idNumber: res.customerInfoList[0].custIdProofs.length > 0 ? res.customerInfoList[0].custIdProofs[0].idNumber : '',
            expiryDate: res.customerInfoList[0].custIdProofs.length > 0 ? this.convertDate(res.customerInfoList[0].custIdProofs[0].expiryDate) : '',
            addressType: address.addressType,
            address1: address.address1,
            address2: address.address2,
            city: address.city,
            country: address.country,
            state: address.state,
            zipCode: address.zipCode,
            phoneNumber: res.customerInfoList[0].phoneNumber,
            landlineNumber: res.customerInfoList[0].landlineNumber,
            primaryEmailAdress: res.customerInfoList[0].primaryEmailAdress
          };
          if (this.accountForm.value.swiftCode == null || this.accountForm.value.swiftCodeEnable == null) {
            console.log(this.accountForm.value.swiftCode);
            this.flag = false;
          }
          console.log("queryParams", queryParams);
          console.log("Residence status:", res.customerInfoList[0].residenceStatus);
          console.log("name", queryParams.firstName, "", queryParams.lastName);
          if (res.customerInfoList[0].custStatus=="APPROVED"){
            this.assignCustomerName(queryParams.firstName, queryParams.lastName, queryParams.middleName);
          }
          this.cifNumberPrimary = cifNumber;
          console.log("cifNumberPrimary::", this.cifNumberPrimary);
          this.ls.setItem("CIF_NUM_PRIMARY", this.cifNumberPrimary);
          this.api.sendCIF(queryParams);
        }
        this.cdr.markForCheck();

      });
    }
  }

  assignCustomerName(firstName: any, lastName: any, middleName) {
    if (middleName) {

      this.name = firstName + " " + middleName + " " + lastName;
      this.ls.setItem("NAME", this.name);

    }
    else {
      this.name = firstName + " " + lastName;
      this.ls.setItem("NAME", this.name);

    }
    console.log("this.name", this.name);
    this.accountForm.get('customerName').setValue(this.name);
    console.log("Customer Name::", this.name)
    console.log(this.accountForm);

  }

  ngOnDestroy() {
    this.dataService.sendNavParam({});
  }
}
