import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from '../account.service';

@Component({
  selector: 'app-intrest-edit',
  templateUrl: './intrest-edit.component.html',
  styleUrls: ['./intrest-edit.component.scss']
})
export class IntrestEditComponent implements OnInit {

  interestForm: FormGroup;
  accountId: any;
  intrestFlag: Boolean;
  interestResp: any;
  accountInfo: any;
  constructor(private accountService: AccountService, private snack: MatSnackBar, private ls: LocalStoreService, private api: ApiService, private router: Router, private formBuilder: FormBuilder) { }

  ngOnInit(): void {
    this.accountId = this.ls.getItem('accountId');
    this.accountInfo = this.ls.getItem('EDIT-CREATEACCCIF');
    if(this.accountInfo){
      this.accountId=this.accountInfo.businessProductAccount.accountId
    }
    this.api.getIntrestValues(this.accountId).subscribe(resp => {
      console.log("backend resp", resp)
      this.interestResp = resp;
      this.check(resp === null)
      if (resp != null) {
        console.log("backend resp", resp.length);
        this.onChangeInterest(resp.length);
        this.in.patchValue(resp);
      }
    })


    this.interestForm = this.formBuilder.group({
      interest: new FormArray([]),
    });
    if (this.interestResp != null) {
      this.onChangeInterest(this.interestResp.length);
    }

  }
  assign(accountId) {
    // this.accountId = accountId;
  }
  onChangeInterest(e) {

    const numberOfItems = e || 0;
    if (this.in.length < numberOfItems) {
      for (let i = this.in.length; i < numberOfItems; i++) {
        this.in.push(
          this.formBuilder.group({
            intrestType: [""],
            intrestRateApplicable: [""],
            marginIn: ["", [Validators.required, Validators.max(100), Validators.min(0)]],
            effectiveRate: ["", [Validators.required, Validators.max(100), Validators.min(0)]],
            accountId: [""],
            createdDate: [""],
            createdBy: [""],
            lastModifiedDate: [""],
            lastModifiedBy: [""],
            id: ['']
          })
        );
      }
    } else {
      for (let i = this.in.length; i >= numberOfItems; i--) {
        this.in.removeAt(i);
      }
    }
  }

  // convenience getters for easy access to form fields
  get f() {
    return this.interestForm.controls;
  }
  get in() {
    return this.f.interest as FormArray;
  }

  goBack() {
    console.log(this.accountId);
    this.router.navigate([`/home/application/enrichment/list/${this.accountId}`]);
  }
  appendPercentage(event) {
    // console.log(event.key)
    console.log(event.target.value);

    if (!event.target.value.length) return;

    if (event.target.value.length === 1) {
      event.target.value = `${event.target.value}%`

    }
    if (event.target.value.length > 1) {
      event.target.value = `${event.target.value.slice(0, -1)}%`
    }
  }

  onSubmit(form) {
    console.log("form", form.interest);
    form.interest = form.interest.map(interest => ({
      ...interest,
      accountId: this.accountId,
    }))
    console.log("form value", form);
    console.log("form value", this.interestForm.value);

    this.api.intrestDetailsSave(form.interest).subscribe(resp => {
      this.snack.open('Interest Details Saved!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
      console.log("backend response from intrest saving", resp)
      this.ls.setItem('ENRICHMENT_INTEREST_DONE', true);
      this.accountService.refresh.next();
      this.router.navigate([`/home/application/enrichment/chargeEdit/${this.accountId}`]);
    }, err => {
      this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    })
  }
  check(accountType) {

    if (accountType) {

      console.log(this.snack.open)
      this.snack.open('Interest detail is empty', 'Error', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    }
  }
}


