import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from '../../account.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { UrlService } from '../../url.service';

@Component({
  selector: 'app-approval-details',
  templateUrl: './approval-details.component.html',
  styleUrls: ['./approval-details.component.scss']
})
export class ApprovalDetailsComponent implements OnInit {
  approvalForm: FormGroup;
  accountId: any;
  isEdit:boolean = false;
  app: any;
  previousUrl: string = '';
  arrayAccountType: string[];
  arrayAccountCurrency: string[];
  arrayUserRecomendation: string[];
  userName: string;
  accountInfo: any;
  constructor(private router: Router,
    private snack: MatSnackBar,private urlService:UrlService,private accountService: AccountService, private formBuilder: FormBuilder, private api: ApiService, private ls: LocalStoreService) { }


  ngOnInit(): void {
    this.userName = localStorage.getItem('userName');
    this.accountId = this.ls.getItem('accountId');
    this.accountInfo = this.ls.getItem('EDIT-CREATEACCCIF');
    if(this.accountInfo){
      this.accountId=this.accountInfo.businessProductAccount.accountId
    }
    setTimeout(() => {
      this.getAccountDetailsInfo(this.accountId);
    }, 1);
    
    this.getAccountType("account_type");
    this.getAccountCurrency("currency");
    this.getUserRecomendation("user_recomendation")
    this.urlService.previousUrl$
    .subscribe((previousUrl: string) => {
        this.previousUrl = previousUrl
    });

    // this.approvalForm.get('accountType').disable();
    // this.approvalForm.get('accountBranch').disable();
    // this.approvalForm.get('productAccountId').disable();
    // this.approvalForm.get('businessProductName').disable();
  }

  buildAccountForm(item) {
    this.approvalForm = this.formBuilder.group({
      accountId: [item ? item.accountId : null,],
      accountType: [item ? item.accountType : '',],
      businessProductName: [item ? item.businessProductName : '',],
      productCode: [item ? item.businessProductName : '',],
      description: [item ? item.description : '',],
      accountBranch: [item ? item.accountBranch : '',],
      accountCurrency: [item ? item.accountCurrency : '',],
      applicationDate: [item ? item.applicationDate : '',],
      fundTheAccount: [item ? item.fundTheAccount : '',],
      fundBy: [item ? item.fundBy : '',],
      amount: [item ? item.amount : '',],
      overDraftRequested: [item ? item.overDraftRequested : '',],
      productDescription: [item ? item.productDescription : '',],
      userRecommendation: [item ? item.userRecommendation : '',],
      createdBy :[this.userName],
    });

  }

  getAccountDetailsInfo(accountId) {
    this.api.approvalAccountFetch(accountId).subscribe(resp => {
      console.log("approval details", resp);
      this.buildAccountForm(resp);
      this.app=resp
      this.isEdit = true;
    }, (err: any) => {
      console.log(err);
      this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    })
  }

  goBack() {
    // this.router.navigate([`${this.previousUrl}`]);
    this.router.navigate([`home/application/approval/list/${this.accountId}`]);

  }

  getAccountCurrency(key: string) {
    this.api.getDropDownValues(key).subscribe((data) => {
      this.arrayAccountCurrency = data;
    });
  }

  getAccountType(key: string) {
    this.api.getDropDownValues(key).subscribe((data) => {
      this.arrayAccountType = data;
    });
  }
  
  getUserRecomendation(key: string) {
    this.api.getDropDownValues(key).subscribe((data) => {
      this.arrayUserRecomendation = data;
    });
  }

  onSubmit(form) {
    console.log(form);
    // this.router.navigate([`/home/application/digital-sign/home/${this.accountId}`]);
    this.api.approvalUpdate(form).subscribe(resp => {
      console.log("resp after updating", resp)
      this.ls.setItem('approvalStage', true);
      this.ls.setItem('APPROVAL_DETAILS_DONE', true);
      
      this.accountService.refresh.next();
      this.snack.open('Approval Details Saved!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
      this.router.navigate([`/home/application/digital-sign/home/${this.accountId}`]);
      

    }) 
  }

  done() {
    this.ls.removeItem('appEntryStage');
    this.ls.removeItem('ACC_DONE');
    this.ls.removeItem('ACC_OPEN_DONE');
    this.ls.removeItem('ACC_SERVICE_DONE');
    this.ls.removeItem('ACC_MANDATE_DONE');
    this.ls.removeItem('ACC_NOMINEE_DONE');

    this.ls.removeItem('limitEntryStage');
    this.ls.removeItem('LIMIT_SECURED_DONE');
    this.ls.removeItem('LIMIT_UNSECURED_DONE');
    this.ls.removeItem('LIMIT_FIN_DONE');

    // Parent 3
    this.ls.removeItem('enrichmentStage');
    // Subnave of parent 3
    this.ls.removeItem('ENRICHMENT_CHARGES_DONE');
    this.ls.removeItem('ENRICHMENT_INTEREST_DONE');

    // PArent 4
    this.ls.removeItem('assessmentStage');
    // Subnave of parent 4
    this.ls.removeItem('ASSESSMENT_QA_DONE');
    this.ls.removeItem('ASSESSMENT_REPORT_DONE');
    // PArent 5
    this.ls.removeItem('fundingStage');
    // Subnave of parent 5
    this.ls.removeItem('FUNDING_INI_DONE');

    // Parent 6
    this.ls.removeItem('approvalStage');
    // Parent 6 childs nav
    this.ls.removeItem('APPROVAL_DETAILS_DONE');
    this.ls.removeItem('accountId');
    this.ls.removeItem('eSignStage');
    this.ls.removeItem('cId');
    this.accountService.refresh.next();
  }

  ngOnDestroy(){
    this.isEdit = false;
  }

}