import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DialogService } from 'app/shared/services/dialogs/dialog.service';
import { rumangoAnimations } from 'app/shared/animations/rumango-animations';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from '../account.service';
import 'rxjs/add/operator/filter';

@Component({
  selector: 'app-application-entry-stage',
  templateUrl: './application-entry-stage.component.html',
  styleUrls: ['./application-entry-stage.component.scss'],
  animations: rumangoAnimations
})
export class ApplicationEntryStageComponent implements OnInit {

  accountId: any;
  isAppEntryStageDone: boolean = false;
  isAccountDetailsScreen: boolean = false;
  isAccountOpenScreen: boolean = false;
  isAccountServiceScreen: boolean = false;
  isAccountMandateScreen: boolean = false;
  isAccountNomineesScreen: boolean = false;
  accountInfo: any;
  constructor(private router: Router,private dialogService : DialogService,private route: ActivatedRoute,private ls: LocalStoreService,private cdr:ChangeDetectorRef ,private accountService:AccountService) { }


  ngOnInit() {
    this.accountId = this.ls.getItem('accountId');
    this.isAppEntryStageDone = this.ls.getItem('appEntryStage');
    this.accountInfo = this.ls.getItem('EDIT-CREATEACCCIF');
    console.log(this.accountInfo,"AccountInfo");
    // if(this.accountInfo){
    //   // this.isAppEntryStageDone = this.ls.getItem('appEntryStage');
    //   this.ls.setItem('appEntryStage',true);
    //   this.ls.setItem('ACC_OPEN_DONE', true);
    //   this.ls.setItem('ACC_DONE', true);
    //   this.ls.setItem('ACC_MANDATE_DONE', true);
    //   this.ls.setItem('ACC_SERVICE_DONE', true);
    //   this.ls.setItem('ACC_NOMINEE_DONE', true);
    // }

    this.route.queryParams
      .filter(params => params.account)
      .subscribe(params => {
        console.log(params); // { order: "popular" }
      }
    );

    // Parent 1 childs nav
    this.isAccountDetailsScreen = this.ls.getItem('ACC_DONE');
    this.isAccountOpenScreen = this.ls.getItem('ACC_OPEN_DONE');
    this.isAccountServiceScreen = this.ls.getItem('ACC_SERVICE_DONE');
    this.isAccountMandateScreen = this.ls.getItem('ACC_MANDATE_DONE');
    this.isAccountNomineesScreen = this.ls.getItem('ACC_NOMINEE_DONE');
    console.log(this.accountId);

    this.accountService.refresh.subscribe((res: any) => {
      this.accountId = this.ls.getItem('accountId');
      // Parent 1
      this.isAppEntryStageDone = this.ls.getItem('appEntryStage');
      // Parent 1 childs nav
      this.isAccountDetailsScreen = this.ls.getItem('ACC_DONE');
      this.isAccountOpenScreen = this.ls.getItem('ACC_OPEN_DONE');
      this.isAccountServiceScreen = this.ls.getItem('ACC_SERVICE_DONE');
      this.isAccountMandateScreen = this.ls.getItem('ACC_MANDATE_DONE');
      this.isAccountNomineesScreen = this.ls.getItem('ACC_NOMINEE_DONE');
    
      this.cdr.markForCheck();
    });

    console.log("Check??",this.isAccountServiceScreen, this.isAccountNomineesScreen);
    if(this.accountInfo){
      this.accountId=this.accountInfo.businessProductAccount.accountId;
    }
  }
  goToDetail(item){
    console.log(item);
    if(item.status == ''){
      this.router.navigate([`/home/${item.state}`]);
    }
    else{
      if(item.title == '1. Account Details'){
        this.dialogService.accountDetailsSummaryDialog();
      }
      else if(item.title == '2. Customer Information Details'){
        this.dialogService.customerInformationDetailsSummaryDialog();
      }
    }
  }

  goToAccountDetails(){
    this.dialogService.accountDetailsSummaryDialog();
  }
  
  goToAccont(){
    this.dialogService.accountserviceDialog();
  }
  gotoMandate(){
    this.dialogService.mandateDialog();
  }
  gotoNominee(){
    this.dialogService.nomineeDialog();
  }

  goToCustomerInformation(){
    this.dialogService.customerInformationDetailsSummaryDialog();
  }
  gotocustDetails(){
    this.dialogService.customerInformationDetailsSummaryDialog();
  }
  gotoApplicationEntrySummary(){
    this.router.navigate([`/home/application/application-entry-summary`, this.accountId]);
  }

}
