import { Component, OnInit } from '@angular/core';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-edit-financial-details',
  templateUrl: './edit-financial-details.component.html',
  styleUrls: ['./edit-financial-details.component.scss']
})
export class EditFinancialDetailsComponent implements OnInit {
  accountId:any;
  constructor(private ls:LocalStoreService,private api: ApiService) { }

  ngOnInit(): void {
    this.accountId = this.ls.getItem('accountId');
    this.getFinacialDeatils(this.accountId);
  }
  getFinacialDeatils(accountId)
  {
    this.api.getFinancialDetails(accountId).subscribe(editResp=>
      {
        console.log("edit",editResp);
       
      })
  }
}
