import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { Subscription } from 'rxjs';
import { AccountService } from '../../account.service';
import { UrlService } from '../../url.service';

@Component({
  selector: 'app-report',
  templateUrl: './report.component.html',
  styleUrls: ['./report.component.scss']
})
export class ReportComponent implements OnInit {
  reportForm: FormGroup;
  accountId: any;
  navSubscription: Subscription;
  routeparams: any = [];
  previousUrl: string = '';
  showEditOption: boolean = false;
  minDate = new Date();
  accountInfo: any;

  constructor(private router: Router, private urlService:UrlService, private api: ApiService,  private dataService: DataService,
    private snack: MatSnackBar, private fb: FormBuilder, private ls: LocalStoreService, private accountService: AccountService) { }

  ngOnInit(): void {
    this.accountId = this.ls.getItem('accountId');
    this.accountInfo = this.ls.getItem('EDIT-CREATEACCCIF');
    if(this.accountInfo){
      this.accountId=this.accountInfo.businessProductAccount.accountId
    }
    let isReportScreen: boolean = JSON.parse(this.ls.getItem('ASSESSMENT_REPORT_DONE'));
   
    this.urlService.previousUrl$
    .subscribe((previousUrl: string) => {
        this.previousUrl = previousUrl
    });
    setTimeout(() => {
      if (this.accountId != null && isReportScreen) {
        this.showEditOption = true;
        this.getAllData();
      } else {
        this.showEditOption = false;
        this.buildReportForm();
      }
    }, 1);
  

  }


  buildReportForm(data?){
    this.reportForm = this.fb.group({
      customerId: [this.accountId != null ? this.accountId : ''],
      limitId: [data ? data.limitId : '', [Validators.required]],
      overdraftLimitAmount: [data ? data.overdraftLimitAmount : '', [Validators.required]],
      recommendedOverdraftLimit: [data ? data.recommendedOverdraftLimit : '', [Validators.required]],
      limitStartDate: [data? data.limitStartDate : '', [Validators.required]],
      limitEndDate: [data ? data.limitEndDate : '', [Validators.required]],
      accountId:[this.accountId != null ? this.accountId : '']
    });
  }

  getReportData(){
    this.api.getAssessmentReportDetails(this.accountId)
      .subscribe((response:any) => {
       console.log(response);
       this.reportForm.patchValue(response);
    });
  }


  goBack() {
    this.router.navigate([`${this.previousUrl}`]);
  }

  saveReports(form) {
    this.api.saveAssessmentReports(form).subscribe((resp) => {
      if(resp){
        this.snack.open('Assessment Report Saved!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.ls.setItem('assessmentStage', true);
        this.ls.setItem('ASSESSMENT_REPORT_DONE', true);
        this.accountService.refresh.next();
        let fundBy = this.ls.getItem('fundByStatus');
        console.log("fund",fundBy);
        // if(fundBy == true){
          if(fundBy.isActive == true){
        this.router.navigate([`/home/application/funding/list/${this.accountId}`]);
        }
        else{
          this.router.navigate([`/home/application/approval/list/${this.accountId}`]);
        }
      }
      else{
        this.ls.setItem('ASSESSMENT_REPORT_DONE', false);
      }
    }, err => {
      this.snack.open('Server Error!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    });
  }

  done() {}

  getAllData(){
    this.api.getAssessmentReportDetails(this.accountId)
    .subscribe((response:any) => {
     this.buildReportForm(response);
    });
  }

  ngOnDestroy() {
    this.dataService.sendNavParam([]);
  }

  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  numberOnly(event): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;

  }
}
