  ngOnInit() {

    this.accountId = this.ls.getItem('accountId');
    this.loggedInUser = '1234578';
    let isSecureDraftScreen: boolean = JSON.parse(this.ls.getItem('LIMIT_SECURED_DONE'));
    this.getAllBranch();
    this.getCollateralType("collateral_type");
    // this.getRateValue("currency");
    this.getCurrencyDetails();
    this.urlService.previousUrl$
      .subscribe((previousUrl: string) => {
        this.previousUrl = previousUrl
      });

    if (this.accountId != null && isSecureDraftScreen) {
      this.showEditOption = true;
      this.getSecuredOverDraft();
    } else {
      this.showEditOption = false;
      this.buildCollateralForm();
    }
    this.otherDocForm = this.formBuilder.group({
      otherDoc_array: this.formBuilder.array([])
    });

  }

  getSecuredOverDraft() {
    this.api.securedOverdraftSummary(this.accountId)
      .subscribe((result: any) => {
        this.secureOverdraftData = result.securedOverDrafts;
        this.secureOverdraftData.forEach((element,index)=>{
          this.docUrl = this.secureOverdraftData[index].documentUrl;
          this.fileNam = this.secureOverdraftData[index].fileName;
          console.log(this.docUrl);
       });  
        console.log("data::", this.secureOverdraftData, this.secureOverdraftData[0].documentUrl);
        this.buildCollateralForm(this.secureOverdraftData, true);
      })
  }
  keyPressAlphanumeric(event) {
    var inp = String.fromCharCode(event.keyCode);
    if (/[a-zA-Z0-9]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }

  buildCollateralForm(data?, isResponse?: boolean) {
    this.collateralForms = this.formBuilder.group({
      collaterals: this.formBuilder.array([this.addCollateralFormGroup()])
    });
    
    if (isResponse) {
      this.addFormGroup(data);
      this.c.patchValue(data);
    }
  }

