  onChangeApplicants(e) {
    console.log(e.value);
    const numberOfCollaterals = e.value || 0;
    if (this.c.length < numberOfCollaterals) {
      for (let i = this.c.length; i < numberOfCollaterals; i++) {
        this.c.push(this.formBuilder.group({
          collateralType: [''],
          collateralCurrency: [''],
          accountId: this.accountId,
          collateralValue: [''],
          attributes: [''],
          dimensions: [''],
          securityReferenceNumber: [''],
          branchCode: [''],
          availableLinkageAccount: [''],
          linkagePercentage: [''],
          overdraftLimitAmount: [''],
          limitStartDate: [''],
          limitEndDate: [''],
          rateValue: [''],
          rate: [''],
          spread: [''],
          finalAmount: [''],
          documentUrl:  [''],
          fileName:  ['']
        }));
      }
    } else {
      for (let i = this.c.length; i >= numberOfCollaterals; i--) {
        this.c.removeAt(i);
      }
    }
  }


