import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ApiService } from 'app/shared/services/api.service';
import { Router } from '@angular/router';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { MatSnackBar } from '@angular/material/snack-bar';
@Component({
  selector: 'app-view-approval-deatils',
  templateUrl: './view-approval-deatils.component.html',
  styleUrls: ['./view-approval-deatils.component.scss']
})
export class ViewApprovalDeatilsComponent implements OnInit {
  approvalForm: FormGroup;
  disableField:boolean;
  accountId:any;
  appArr:any=[];
  accountInfo: any;
  constructor(private formBuilder: FormBuilder,
    private api: ApiService,
    private router: Router,
    private ls:LocalStoreService,
    private snack:MatSnackBar,
    private dialogRef: MatDialogRef<ViewApprovalDeatilsComponent>,
  ) { }

  ngOnInit(): void {
    this.disableField=true;
    this.accountId = this.ls.getItem('accountId');
    this.accountInfo = this.ls.getItem('EDIT-CREATEACCCIF');
    if(this.accountInfo){
      this.accountId=this.accountInfo.businessProductAccount.accountId
    }
    this.approvalForm = this.formBuilder.group({
      accountType: ['', []],
      businessProductName: ['', []],
      productImage: ['', []],
      description: ['', []],
      accountBranch: ['', []],
      accountCurrency: ['', []],
      applicationDate: ['', []],
      fundTheAccount: ['', []],
      fundBy: ['', []],
      amount: ['', []],
      overDraftRequested: ['', []],
      productAccountId: ['', []],
      productDescription: ['', []],
      userRecommendation: ['', []]



    });
    setTimeout(() => {
      this.getApprovalDetails(this.accountId);
    }, 1);
   
   
  }
  getApprovalDetails(accountId)
  {
    this.api.approvalAccountFetch(accountId).subscribe(resp => {
      console.log("approval details", resp)
      this.appArr=resp;
      this.appArr.accountCurrency=this.appArr.accountCurrency.toLowerCase();
      // if(resp)
      // {console.log("inside if");
      // this.appArr.push(resp);
      // }
      // else{
      //   console.log("no resp");
    
      // console.log("inid else1");
      // this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
      //   duration: 4000,
      //   verticalPosition: 'top',
      //   horizontalPosition: 'right'
      // });
    
      // }
     

    },
    (err: any) => {
      console.log("err",err);
      this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    })
  }
  
  onEditOfApprovalStging() {
    // this.router.navigateByUrl('/home/application/approval/details');
    this.router.navigate([`/home/application/approval/details/${this.accountId}`]);
    console.log("sending", this.approvalForm);
    this.api.sendApprovalStgingData(this.approvalForm.value);
  }
}
