  convertDate(value) {
    let date = moment(value).format('YYYY-MM-DD'); // ISO 8601 format
    // let date = new Date(value).format('yyyy-MMM-dd');
    console.log("date", date);
    if (date)
      return date;
  }


  buildCustomerForm(data?) {
    this.applicantForms = this.formBuilder.group({
      productAccountId: data ? data.productAccountId : this.accountId,
      ownership: data ? data.ownership : 'Self',
      numOfApplicants: data ? data.numOfApplicants : 1,
      applicantDetails: this.addCustFormGroup()
    });
  }

  toggleChanges() {
    if (this.applicant.get('existingCustomer').value == true) {
      this.applicant.get('cifNumber').enable();
      this.applicant.get('cifNumber').setValidators([Validators.required]);
      this.applicant.get('cifNumber').updateValueAndValidity();
    }
    else {
      this.applicant.get('cifNumber').disable();
      this.applicant.get('cifNumber').clearValidators();
      this.applicant.get('cifNumber').updateValueAndValidity();
    }
  }

  toggleAddForm() {
    this.showNewForm = !this.showNewForm;
  }

  addCustFormGroup(): FormGroup {
    return this.formBuilder.group({
      existingCustomer: [null, []],
      primaryCustomer: [null, []],
      cifNumber: [''],
      customerId: [''],
      dependentId: [null],
      prefix: ['', Validators.required],
      firstName: ['', Validators.required],
      middleName: [''],
      lastName: ['', Validators.required],
      gender: ['', Validators.required],
      dateOfBirth: ['', Validators.required],
      placeOfBirth: ['', Validators.required],
      residenceStatus: ['', Validators.required],
      nationality: ['', Validators.required],
      countryOfResidence: ['', Validators.required],
      occupationType: ['', Validators.required],
      idType: ['', Validators.required],
      idNumber: ['', Validators.required],
      expiryDate: [''],
      addressType: ['', Validators.required],
      address1: ['', [Validators.required,Validators.pattern('.*[a-zA-Z].*')]],
      address2: ['', [Validators.required,Validators.pattern('.*[a-zA-Z].*')]],
      city: ['', Validators.required],
      state: ['', Validators.required],
      country: ['', Validators.required],
      zipCode: ['', [Validators.required]],
      phoneNumber: ['', [Validators.required, Validators.minLength(3), Validators.maxLength(10), Validators.pattern("^[0-9]{10,12}$")]],
      landlineNumber: ['', [Validators.pattern("^[0-9]{10,12}$")]],
      primaryEmailAdress: ['', [Validators.required,
      Validators.email]],
    })
  }


  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }

  keyPressAlphanumeric(event) {
    var inp = String.fromCharCode(event.keyCode);
    if (/[a-zA-Z0-9]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }


  validateNumber(event) {
    const keyCode = event.keyCode;

    const excludedKeys = [8, 37, 39, 46];

    if (!((keyCode >= 48 && keyCode <= 57) ||
      (keyCode >= 96 && keyCode <= 105) ||
      (excludedKeys.includes(keyCode)))) {
      event.preventDefault();
    }
  }

