  getStateName(country,isAdd?) {
    if(isAdd){
this.applicant.get('zipCode').setValue('');
this.applicant.get('state').setValue('');
this.applicant.get('city').setValue('');
    }
    this.apiService.getAllCountries().subscribe((data: any) => {
      for(let i=0; i<data.length;i++){       
        if(data[i].countryName == country){
          this.apiService.getStateByCountryId(data[i].id).subscribe((resp: any) => {
          this.arrayState = resp
          })
          break;   
        }
      }
    })
  }


