import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from '../account.service';
import { IntialFundingDialogComponent } from './intial-funding-dialog/intial-funding-dialog.component';

@Component({
  selector: 'app-funding-stage',
  templateUrl: './funding-stage.component.html',
  styleUrls: ['./funding-stage.component.scss']
})
export class FundingStageComponent implements OnInit {

  objIndex: number;
  detailsShowOrViewRef: any = 'Add Details';
  processCompleted: boolean = false;
  accountId:any;
  isFundingStageDone:boolean = false;
  isInitialFundScreen:boolean = false;
  isReportScreen:boolean = false;
  isQualitativeScreen:boolean = false;

  constructor(private router: Router,
    public dialog: MatDialog,
    private ls: LocalStoreService,
    private accountService:AccountService,
    private cdr:ChangeDetectorRef) { }

  ngOnInit(): void {
// account id getting from local storage

   this.accountId = this.ls.getItem('accountId');
    this.isFundingStageDone = this.ls.getItem('fundingStage');
    this.isInitialFundScreen = this.ls.getItem('FUNDING_INI_DONE');
    this.isReportScreen = this.ls.getItem('ASSESSMENT_REPORT_DONE');
    this.isQualitativeScreen = this.ls.getItem('ASSESSMENT_QA_DONE');

    this.accountService.refresh.subscribe((res: any) => {
      this.accountId = this.ls.getItem('accountId');
      // Parent 5
      this.isFundingStageDone = this.ls.getItem('fundingStage');
      // Parent 5 childs nav
      this.isInitialFundScreen = this.ls.getItem('FUNDING_INI_DONE');
      this.isReportScreen = this.ls.getItem('ASSESSMENT_REPORT_DONE');
      this.isQualitativeScreen = this.ls.getItem('ASSESSMENT_QA_DONE');
    
      this.cdr.markForCheck();
    });

 
  }
  
  goToViewSummary() {
    
    // this.router.navigate([`/home/application/funding/summary/`, this.accountId]);
    console.log("efg");
    this.router.navigate([`/home/application/funding/summary/${this.accountId}`]);
   // this.router.navigate([`/home/application/funding/testing/${this.accountId}`]);
    console.log("abcd");
  }

  goToAddDetail() {
    this.router.navigate([`/home/application/funding/details/`, this.accountId]);
  }


  openDialog(): void {
    const dialogRef = this.dialog.open(IntialFundingDialogComponent, {
      width: '782px',
      height: '380px'
      // data: {name: this.name, animal: this.animal}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      // this.animal = result;
    });
  }

}
