import { ChangeDetectorRef, Component, OnInit, ViewChild, Renderer2, ElementRef, Inject } from '@angular/core';
import { FormBuilder, FormGroup, FormArray, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { AppConfirmService } from 'app/shared/services/app-confirm/app-confirm.service';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';
import { ActivatedRoute, Router } from '@angular/router';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { DataService } from 'app/shared/services/data.service';
import * as moment from 'moment';
import { AccountService } from 'app/views/home/account.service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'app-account-open-popup',
  templateUrl: './account-open-popup.component.html',
  styleUrls: ['./account-open-popup.component.scss']
})
export class AccountOpenPopupComponent implements OnInit {
  submitted = false;
  formData = {}
  console = console;
  applicantForms: FormGroup;
  accountId: string;

  arrayPrefix: any[] = [];
  arrayGender: any[] = [];
  arrayCountry: any[] = [];
  arrayState: any[] = [];
  arrayCity: any[] = [];
  arrayResidence: any[] = [];
  arrayDocumentType: any[] = [];
  applicantDetailsArray: any[] = [];
  toggle: boolean = false;
  // customerDetails: IcustProductCustomerInfo = new IcustProductCustomerInfo();
  customerDetails: any = {};
  applicantDetails = [];
  navSubscription: any;
  routeparams: any;
  showEditOption: boolean = false;
  custInfo: string[];
  tomorrow = new Date();
  maxDate = new Date();
  minDate = new Date();
  addNew: boolean = true;

  toggleClick: boolean = false;
  existingCustomer: boolean;
  productCustomerId: any;
  isProductCustomerIdExist: boolean = false;
  isAccountOpenScreen: boolean = false;
  previousUrl: string = '';
  addFunc: boolean = true;
  index: any;
  entityName: string;
  arrayOwnership: string[];
  isAllSameDigitVal: boolean= false;


  constructor( @Inject(MAT_DIALOG_DATA) public data: any,
  public dialogRef: MatDialogRef<AccountOpenPopupComponent>,private router: Router, private route: ActivatedRoute,private ls: LocalStoreService, private apiService: ApiService, private formBuilder: FormBuilder, private confirmService: AppConfirmService, private cdr: ChangeDetectorRef, private appApiService: ApplicationEntryStageService,
    private snack: MatSnackBar, private accountService: AccountService) {
    this.tomorrow.setDate(this.tomorrow.getDate());
  }

  ngOnInit(): void {
    this.accountId = this.ls.getItem('accountId');
    console.log(this.accountId);
    this.productCustomerId = this.ls.getItem('PRODUCT_CUSTOMER_ID');
    this.isAccountOpenScreen = JSON.parse(this.ls.getItem('ACC_OPEN_DONE'));
    console.log("check if", this.accountId, this.isAccountOpenScreen);

    this.getPrefixValue("prefix");
    this.getGenderValue("gender");
    this.getOwnershipValue("ownership");
    this.getResidenceDetails("residence");
    this.getCountryName("country");
    // this.getStateName("state");
    // this.getCityName("city");

    this.getDocumentType("document_type");

    this.buildCustomerForm();
  }

  buildCustomerForm(data?) {
    console.log("data Check ??", data);
    this.applicantForms = this.formBuilder.group({
      productAccountId: data ? data.productAccountId : this.accountId,
      ownership: data ? data.ownership : 'Self',
      numOfApplicants: data ? data.numOfApplicants.toString() : '',
      applicantDetails: this.addCustFormGroup()
    });
  }

  toggleChanges(i) {
    // let control = this.a.controls[i] as FormGroup;
    // console.log(control.get('existingCustomer').value);
    // if (control.controls['existingCustomer'].value == true) {
    //   control.controls['cifNumber'].setValidators([Validators.required]);
    //   control.controls['cifNumber'].updateValueAndValidity();
    //   control.controls['cifNumber'].markAsTouched();
    //   // this.toggleClick = true;
    //   // this.existingCustomer = true;
    // }
    // else {
    //   // this.toggleClick = false;
    //   control.controls['cifNumber'].clearValidators();
    //   control.controls['cifNumber'].updateValueAndValidity();
    //   control.controls['cifNumber'].markAsTouched();
    // }
  }

  letterOnly(event) {
    var charCode = event.keyCode;
            if ((charCode > 64 && charCode < 91) || (charCode > 96 && charCode < 123) || charCode == 8)
                return true;
            else
                return false;
  }

  onKeyPress(event: any) {
    const number = /[0-9\+\-\ ]/;
    let inputCharacter = String.fromCharCode(event.charCode);
    if (event.keyCode != 8 && !number.test(inputCharacter)) {
      event.preventDefault();
      console.log("event:", event)
    }
  }

  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }

  validateNumber(event) {
    const keyCode = event.keyCode;
    const excludedKeys = [8, 37, 39, 46];
    if (!((keyCode >= 48 && keyCode <= 57) ||
      (keyCode >= 96 && keyCode <= 105) ||
      (excludedKeys.includes(keyCode)))) {
      event.preventDefault();
    }
  }


  addCustFormGroup(): FormGroup {
    return this.formBuilder.group({
      existingCustomer: [false, []],
      primaryCustomer: [false, []],
      cifNumber: [],
      prefix: ['', Validators.required],
      firstName: ['', Validators.required],
      middleName: [''],
      lastName: ['', Validators.required],
      gender: ['', Validators.required],
      dateOfBirth: ['', Validators.required],
      placeOfBirth: ['', Validators.required],
      residenceStatus: ['', Validators.required],
      nationality: ['', Validators.required],
      countryOfResidence: ['', Validators.required],
      occupationType: ['', Validators.required],
      idNumber: ['', Validators.required],
      idType: ['', Validators.required],
      expiryDate: ['', Validators.required],
      addressType: ['', Validators.required],
      building: ['', Validators.required],
      street: ['', Validators.required],
      locality: ['', Validators.required],
      city: ['', Validators.required],
      state: ['', Validators.required],
      country: ['', Validators.required],
      zipCode: ['', [Validators.required]],
      phoneNumber: ['', [Validators.required, Validators.minLength(3), Validators.maxLength(10), Validators.pattern("^[0-9]{10,12}$")]],
      landlineNumber: ['', [Validators.required, Validators.pattern("^[0-9]{10,12}$")]],
      primaryEmailAdress: ['', [Validators.required,
      Validators.email]],
    })
  }

  get f() { return this.applicantForms.controls; }
  get applicant() { return this.f.applicantDetails; }


  convertDate(value) {
    let date = moment(value).format('YYYY-MM-DD'); // ISO 8601 format
    // let date = new Date(value).format('yyyy-MMM-dd');
    console.log("date", date);
    if (date)
      return date;
  }


  getPrefixValue(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayPrefix = data;
    });
  }

  getGenderValue(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayGender = data;
    });
  }

  getCountryName(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayCountry = data;
      console.log("-->", this.arrayCountry)
    });
  }

  getOwnershipValue(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayOwnership = data;
    });
  }

  getStateName(country) {
    this.entityName = "state";
    console.log("State -- ", country)
    this.apiService.getDropDownValues1(this.entityName, country).subscribe((data) => {
      this.arrayState = data;
      console.log("-->", this.arrayState)
    });
  }

  getCityName(state: any) {
    this.entityName = "city";
    console.log(state)
    this.apiService.getDropDownValues1(this.entityName, state).subscribe((data) => {
      this.arrayCity = data;
      console.log("-->", this.arrayCity)
    });
  }


  getResidenceDetails(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayResidence = data;
    });
  }

  getDocumentType(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayDocumentType = data;
    });
  }

  onSubmit(index: any) {

    this.submitted = true;

    if (this.applicantForms.invalid) {
      console.log("test");
      return;
    }

    let payloadObj: any = {};
    const control = <FormArray>this.applicantForms.controls['applicantDetails'].value;

    for (let i = 0; i < control.length; i++) {

      let customerInfo: any = {}
      let addressObj: any = {};
      let custIdProofs: any = {};
      let custIdProofsArray: any[] = [];
      let addressObjArray: any[] = [];

      addressObj.addressType = control[i].addressType;
      addressObj.address1 = control[i].building + "," + control[i].street + "," + control[i].locality;
      addressObj.address2 = control[i].state;
      addressObj.city = control[i].city;
      addressObj.country = control[i].country;
      // addressObj.state = control[i].state;
      addressObj.city = control[i].city;
      addressObj.zipCode = control[i].zipCode;
      addressObjArray.push(addressObj);

      custIdProofs.expiryDate = this.convertDate(control[i].expiryDate);
      custIdProofs.idType = control[i].idType;
      custIdProofs.idNumber = control[i].idNumber;
      custIdProofsArray.push(custIdProofs);

      customerInfo.userAddress = addressObjArray;
      customerInfo.custIdProofs = custIdProofsArray;
      customerInfo.phoneNumber = control[i].phoneNumber;
      customerInfo.primaryEmailAdress = control[i].primaryEmailAdress;
      customerInfo.prefix = control[i].prefix;
      customerInfo.firstName = control[i].firstName;
      customerInfo.middleName = control[i].middleName;
      customerInfo.lastName = control[i].lastName;
      customerInfo.cifNumber = control[i].cifNumber;
      customerInfo.gender = control[i].gender;
      customerInfo.dateOfBirth = this.convertDate(control[i].dateOfBirth);
      customerInfo.nationality = control[i].nationality;
      customerInfo.custAccount = null;
      customerInfo.landlineNumber = control[i].landlineNumber;
      customerInfo.createdBy = "ADMIN";
      customerInfo.createdTime = "2021-Jul-21 00:34:39";
      customerInfo.modifiedBy = "ADMIN";
      customerInfo.modifiedTime = "2021-Jul-22 11:55:38";


      payloadObj.productCustomerId = this.ls.getItem('PRODUCT_CUSTOMER_ID');
      payloadObj.existingCustomer = control[i].existingCustomer;
      payloadObj.primaryCustomer = control[i].primaryCustomer;
      payloadObj.residenceStatus = control[i].residenceStatus;
      payloadObj.placeOfBirth = control[i].placeOfBirth;
      payloadObj.countryOfResidence = control[i].countryOfResidence;
      payloadObj.occupationType = control[i].occupationType;
      // payloadObj.cifNumber = control[i].cifNumber;
      payloadObj.customerInfo = customerInfo;

    }
    // payloadObj.applicantDetails = this.applicantDetailsArray;
    console.log(payloadObj);


    console.log("value of productCustomerId ", this.productCustomerId);

    this.appApiService.saveCustomerDetails2(payloadObj)
      .subscribe((data: any) => {
        console.log("Response of service 2 ---> ", data);
        this.ls.setItem('ACC_OPEN_DONE', true);
        this.accountService.refresh.next();

        if (data === 200 || data !== null) {
          this.snack.open('Customer Information Details Saved!', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });

        }
        else if (data === 500) {
          this.snack.open('Not able to connect to server through 2 service', 'INTERNAL SERVER ERROR 2', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
        }
      }, err => {
        console.log(err);
      })
  }

  onChange(event, i) {
    console.log("toggle event", event.checked, "index", i);

    if (event.checked == true) {
      this.toggle = true;
      this.index = i;
    }
    if (event.checked == false && this.index === i) {
      this.toggle = false;
    }
    console.log("this.toggle", this.toggle)
  }

  onReset() {
    // reset whole form back to initial state
    this.submitted = false;
    this.applicantForms.reset();
  }

  isAllSameDigit(event) {
    let count = 0, number = event.target.value;
    if (number != '') {
      for (var i = 0; i < number.length; i++) {
        if (0 == number[i]) {
          count++;
        }
      }
      console.log('number :: ', number);
      console.log('isAllSameDigitVal :: ', count , number.length)
      if (count == number.length) {
        console.log('if loop')
        this.isAllSameDigitVal = true;
      }
      else {
        this.isAllSameDigitVal = false;
      }
    }else{
      this.isAllSameDigitVal = false;
    }
  }

}
