  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }
  keyPressAlphanumeric(event) {
    var inp = String.fromCharCode(event.keyCode);
    if (/[a-zA-Z0-9]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }
  letterOnly(event) {
    var charCode = event.keyCode;
            if ((charCode > 64 && charCode < 91) || (charCode > 96 && charCode < 123) || charCode == 8)
                return true;
            else
                return false;
  }

  onSubmit() {
    for(let i=0;i<this.nomineeForms.value.nominees.length;i++) {
      this.nomineeForms.value.nominees[i].dateOfBirth = this.convertDate(this.nomineeForms.value.nominees[i].dateOfBirth);
      if(this.nomineeForms.value.nominees[i].isNomineeMinor) {
        this.nomineeForms.value.nominees[i].guardianDetails[0].dateOfBirth = 
            this.convertDate(this.nomineeForms.value.nominees[i].guardianDetails[0].dateOfBirth);
      }
    }
    
    this.submitted = true;

    let payload = {
      "productAccountId": this.accountId,
      "nominees": this.nomineeForms.value.nominees
    };
    
    if (this.nomineeForms.invalid) {
      return;
    }

    if (this.showEditOption) {
      console.log("to check nomineeId",this.nomineeForms.value.nominees);
      this.apiService.updateNominee(this.accountId, payload)
        .subscribe((data: any) => {
          console.log(data);
          this.snack.open('Nominee Updated!', 'OK', { duration: 4000, verticalPosition: 'top', horizontalPosition: 'right' });
          
          this.router.navigate([`/home/application/limit-entry-stage/list/${this.accountId}`]);
        }, (err: any) => {
          console.log(err);
        });

    } else {
      this.apiService.createNominee(this.accountId, payload)
        .subscribe((data: any) => {
          console.log(data);
          this.snack.open('Nominee Details Saved!', 'OK', { duration: 4000, verticalPosition: 'top', horizontalPosition: 'right' });
          // this.snack.open('Either Secured Overdraft or Unsecured Overdraft Details are Mandatory!', 'OK', { duration: 4000, verticalPosition: 'top', horizontalPosition: 'right' });
          this.ls.setItem('appEntryStage', true);
          this.ls.setItem('ACC_NOMINEE_DONE', true);
          this.accountService.refresh.next();
          let isActivee = this.ls.getItem('overDraftByStatus') || null;
          this.overDraftByStatus = isActivee != null ? isActivee.isActive : false;
          // this.isActive = this.overDraftByStatus;
          this.console.log("this.overDraftByStatus", this.overDraftByStatus)
          
          // console.log(this.isActive);
          if( this.overDraftByStatus === true){
            console.log( this.overDraftByStatus);
            this.dialogService.alertDialog().subscribe((response) => {
              console.log("response ::", response)
              console.log("active", this.overDraftByStatus)
            //   // if (response == "No") {
            //   //   this.pancard=false;
            //   //   this.changeValue2_Ocr(docVal);
            //   // }
            //   // if (response == "Yes") {
            //   //   console.log("line 183");
            //   // }
            });
            this.router.navigate([`/home/application/limit-entry-stage/list/${this.accountId}`]);
           }
          else{
            console.log("active",isActivee)
            this.router.navigate([`/home/application/limit-entry-stage/list/${this.accountId}`]);
          }
          
        }, (err: any) => {
          console.log(err);
        });
    }
  }

