  removeNominee(i: number) {
    console.log("i", i);
    let nomineeId = this.n.at(i).get('nomineeId').value;
    console.log("nomineeId ::",nomineeId)
    this.confirmService
      .confirm({ title: "Confirm", message: "Are you sure want to remove?", ok: "Yes", cancel: "No" })
      .subscribe(res => {
        console.log("res:: removed ", res);
        if (res) {
          this.apiService.deleteNomineeById(nomineeId, 'ADMIN')
          .subscribe((data:any) => {
            console.log("data", data);
        },err=>{
          const control = <FormArray>this.nomineeForms.controls['nominees'];
          control.removeAt(i);
          if (err.status === 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
            console.log("Deleting length",control.length);
                if(control.length == 0){
            
                  this.ls.setItem('ACC_NOMINEE_DONE', false);
                  this.accountService.refresh.next();
                  // this.router.navigate([`/home/application/list/${this.accountId}`]);
                  this.router.navigate([`/home/application/list`]);
                }
                else{
                  this.onReset();
                  this.gettingNomineeDetail();
                }
                
              
          }
        });
      } else return;
      });
  }

