  isAllSameDigit(event) {
    let count = 0, number = event.target.value;
    if (number != '') {
      for (var i = 0; i < number.length; i++) {
        if (0 == number[i]) {
          count++;
        }
      }
      console.log('number :: ', number);
      console.log('isAllSameDigitVal :: ', count , number.length)
      if (count == number.length) {
        console.log('if loop')
        this.isAllSameDigitVal = true;
      }
      else {
        this.isAllSameDigitVal = false;
      }
    }else{
      this.isAllSameDigitVal = false;
    }
  }
  getCityandStateByZipcode(countryName, zipcode, index) { 
    if (countryName && zipcode.length >= 6) {
      this.apiService.fetchStateCityByZipcode(countryName, zipcode).subscribe((res: any) => {
        if(res){
          this.n.controls[index].get('state').setValue(res.stateName);
          this.n.controls[index].get('city').setValue(res.cityName);
        }
        else{
          this.snack.open("Entered Zipcode does not exists", "OK", {
            duration: 4000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });        }
      })
    }
  }
