import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { AccountDetails } from 'app/shared/models/accountDetails';
import { CustomerInformationDetails } from 'app/shared/models/customerInformationDetails';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { UrlService } from '../../url.service';
import { AccountDetailsSummaryComponent } from '../account-details-summary/account-details-summary.component';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';

@Component({
  selector: 'app-application-entry-summary',
  templateUrl: './application-entry-summary.component.html',
  styleUrls: ['./application-entry-summary.component.scss']
})
export class ApplicationEntrySummaryComponent implements OnInit {
  titleName = "Application Entry Stage";
  // productaccountId = 1166;
  bankPreference: any = [];
  productaccountId : any;
  accounttype: any;
  businessproductName: any;
  accountbranch: any;
  accountcurrency: any;
  applicationdate: any;
  fundTheaccount: any;
  fundby: any;
  overdraftrequested: any;
  amounT: any;
  Custownership: any;
  CustnumOfApplicants: any;
  customerDetails : any [];
  allMandate : any [];
  accountStatement: any;
  statementPreference: any = [];
  statementPreferenceFetched: any[];
  checkBook: any;
  bankChannel: any;
  commChannel: any = [];
  commChannelFetched: any[];
  passBook: any;
  allNominee : any [];
  previousUrl: string = '';
  swiftEnable: boolean=false;
  swiftCode: any;
  swiftCodeEnable: any;
  debitCardRequired: any;
  productAccountIdCust: any;
  customerId: any;
  customerSummary: any;

  constructor( 
    private router:Router,private apiService : ApiService, private loader:AppLoaderService, private appEntryService:ApplicationEntryStageService,
    private localstore: LocalStoreService, private urlService:UrlService) { }

  ngOnInit(): void {
    this.productaccountId = this.localstore.getItem('accountId');
    console.log("Account ID from Local Storage", this.productaccountId);
    this.gettingallDetails(this.productaccountId);
    this.getCustomerInformationDetails();
    this.urlService.previousUrl$
    .subscribe((previousUrl: string) => {
        this.previousUrl = previousUrl
    });
    
  }
  
  gettingallDetails(productaccountId) {
    this.loader.open();
    this.apiService.gettingallDetails(productaccountId).subscribe((applicationEntryResp : any) => {
      this.loader.close();
      console.log("Application Entry Details",applicationEntryResp);
      
      this.allMandate = applicationEntryResp.mandateInfo;
      this.allNominee = applicationEntryResp.nomineeInfo;

      if(applicationEntryResp.accountInfo.swiftCodeEnable==true){
        this.swiftEnable=true;
      }
       this.accounttype = applicationEntryResp.accountInfo.accountType;
       this.businessproductName = applicationEntryResp.accountInfo.businessProductName;
       this.accountbranch = applicationEntryResp.accountInfo.accountBranch;
       this.accountcurrency = applicationEntryResp.accountInfo.accountCurrency;
       this.applicationdate = applicationEntryResp.accountInfo.applicationDate;
       this.fundTheaccount = applicationEntryResp.accountInfo.fundTheAccount;
       this.fundby = applicationEntryResp.accountInfo.fundBy;
       this.amounT = applicationEntryResp.accountInfo.amount;
       this.overdraftrequested = applicationEntryResp.accountInfo.overdraftRequested;
       this.swiftCodeEnable=applicationEntryResp.accountInfo.swiftCodeEnable;
       this.swiftCode=applicationEntryResp.accountInfo.swiftCode
       
     



       this.accountStatement = applicationEntryResp.accServiceInfo.statementFrequency;
       this.checkBook = applicationEntryResp.accServiceInfo.chequebookNeeded; 
       this.debitCardRequired = applicationEntryResp.accServiceInfo.debitCardRequired;
       this.bankChannel = applicationEntryResp.accServiceInfo.bankChannelPref;
       for (let index = 0; index < this.bankChannel.length; index++) {
        const element = this.bankChannel[index];
        if(element.checked) {
          this.bankPreference.push(element.name);
        }
      }
       this.passBook = applicationEntryResp.accServiceInfo.passbookNeeded; 
       this.debitCardRequired = applicationEntryResp.accServiceInfo.debitCardRequired;
       this.statementPreferenceFetched = applicationEntryResp.accServiceInfo.statementPref;
       for (let index = 0; index < this.statementPreferenceFetched.length; index++) {
         const element = this.statementPreferenceFetched[index];
         if(element.checked) {
           this.statementPreference.push(element.name);
         }
       }
       this.commChannelFetched = applicationEntryResp.accServiceInfo.commChannelPref;
       for (let index = 0; index < this.commChannelFetched.length; index++) {
         const element = this.commChannelFetched[index];
         if(element.checked) {
           this.commChannel.push(element.name);
         }
       }
      
    
       this.Custownership = applicationEntryResp.customerInfo.data[0].ownership;
       this.CustnumOfApplicants = applicationEntryResp.customerInfo.data[0].numOfApplicants;
       this.customerDetails =applicationEntryResp.customerInfo.data;
       for (let i = 0; i < this.customerDetails.length; i++) {
        let userAddress = this.customerDetails[i].userAddress;
        let pickOneAddress: any;
        let Address = "";
        if (userAddress.length > 1) {
          pickOneAddress = userAddress.find((item: any) => item.addressType == "permanent");
        } else {
          pickOneAddress = userAddress[0];
        }
        Address = pickOneAddress.city + "," + pickOneAddress.state + "," + pickOneAddress.zipCode + "," + pickOneAddress.country;
        this.customerDetails[i].userAddress = Address;
      }

      }, (err:any) =>{
        this.loader.close();
      });
      console.log(this.allNominee);
  }

  getCustomerInformationDetails() {
    let accountType ='savings';
   this.appEntryService.fetchAccountDependentInfo(this.productaccountId,accountType)
      .subscribe((allCustomernResp: any) => {
        console.log('resp',allCustomernResp)
        this.customerSummary = allCustomernResp.data;
        this.customerId = allCustomernResp.data[0].customerId;
        this.Custownership = allCustomernResp.data[0].ownership;
        this.CustnumOfApplicants = allCustomernResp.data[0].numOfApplicants;
        this.customerDetails = allCustomernResp.data;

        for (let i = 0; i < this.customerDetails.length; i++) {
          let userAddress = this.customerDetails[i].userAddress;
          let pickOneAddress: any;
          let Address = "";
          if (userAddress.length > 1) {
            pickOneAddress = userAddress.find((item: any) => item.addressType == "Permanent");
          } else {
            pickOneAddress = userAddress[0];
          }
          Address = pickOneAddress.city + "," + pickOneAddress.state + "," + pickOneAddress.zipCode + "," + pickOneAddress.country;
          this.customerDetails[i].userAddress = Address;
        }
      }, (err: any) => {
        this.loader.close();
      });
  }


  goBack() {
    this.router.navigate([`${this.previousUrl}`]);
  }


  done() {
    this.router.navigate([`/home/application/list`]);
  }

}
