import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormArray, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { rumangoAnimations } from 'app/shared/animations/rumango-animations';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from '../../account.service';
import { UrlService } from '../../url.service';

@Component({
  selector: 'app-charges',
  templateUrl: './charges.component.html',
  styleUrls: ['./charges.component.scss'],
  animations: rumangoAnimations
})
export class ChargesComponent implements OnInit {

  chargeForm: FormGroup;
  charge: any;
  flag: boolean = false;
  accountType: any;
  isChargesScreen: boolean = false;
  isInterestScreen: boolean = false;
  previousUrl: string = '';
  // numberPattern: "/^\-?\d+$/";
  constructor(private api: ApiService, private urlService:UrlService, private snack: MatSnackBar, private accountService: AccountService, private router: Router, private formBuilder: FormBuilder, private ls: LocalStoreService) { }
  accountId: any;
  currencytype:any;
  currencySymbol: string;
  ngOnInit(): void {

    this.accountId = this.ls.getItem('accountId');
    this.currencytype=localStorage.getItem('CurrencyType');
    console.log(this.currencytype)
    if(this.currencytype==="INR" || this.currencytype==="inr"){
      this.currencySymbol=""
    }else if(this.currencytype==="USD" || this.currencytype==="usd" ){
      this.currencySymbol="$"
    }else if(this.currencytype==="CNY" || this.currencytype==="cny"){
      this.currencySymbol=""
    }else if(this.currencytype==="GBP" || this.currencytype==="gbp"){
      this.currencySymbol=""
    }else if(this.currencytype==="ZAR" || this.currencytype==="zar"){
      this.currencySymbol="R"
    }

    this.isChargesScreen = this.ls.getItem('ENRICHMENT_CHARGES_DONE');
    console.log(" this.isChargesScreen", this.isChargesScreen)

    this.accountType = this.ls.getItem('accountType');
    console.log(this.accountType)
    this.api.getFlexcubeDetails("chargeDetails", this.accountType).subscribe(response => {
      this.onChangeInterest(response.length);
      console.log(response);
      this.check(response=== null)
      this.c.patchValue(response)
    })

    this.api.getChargeValues(this.accountId).subscribe(resp=>{
      console.log("backend resp",resp)
      console.log("backend resp",resp?.length);
      this.onChangeInterest(resp.length);
      this.c.patchValue(resp);
    })

    this.chargeForm = this.formBuilder.group({
      charges: new FormArray([])
    });
    this.onChangeInterest(1);
    // this.onChangeInterest(2);
    this.urlService.previousUrl$
    .subscribe((previousUrl: string) => {
        this.previousUrl = previousUrl
    });
  }
  assign(accountId: any) {
    // this.accountId = accountId;
  }

  onChangeInterest(e) {
    const numberOfItems = e || 0;
    if (this.c.length < numberOfItems) {
      for (let i = this.c.length; i < numberOfItems; i++) {
        this.c.push(this.formBuilder.group({
          //         private Long id;
          // private String intrestType;
          // private Double amount;
          // private Boolean waiver;
          // private String accountId;
          // @JsonFormat(shape = JsonFormat.Shape.STRING, pattern = "yyyy-MMM-dd HH:mm:ss")
          // private Timestamp createdDate;
          // private Long createdBy;
          // @JsonFormat(shape = JsonFormat.Shape.STRING, pattern = "yyyy-MMM-dd HH:mm:ss")
          // private Timestamp lastModifiedDate;
          // private Long lastModifiedBy;
          intrestType: [''],
          amount: ['', Validators.required],
          waiver: [false],
          accountId: [this.accountId],
          createdDate: [''],
          craetedBy: [''],
          lastModifiedDate: [''],
          lastModifiedBy: [''],
          id: ['']

        }));
      }
    } else {
      for (let i = this.c.length; i >= numberOfItems; i--) {
        this.c.removeAt(i);
      }
    }
  }

  // convenience getters for easy access to form fields
  get f() { return this.chargeForm.controls; }
  get c() { return this.f.charges as FormArray; }


  goBack() {
    this.router.navigate([`home/application/enrichment/interest/${this.accountId}`]);
  }

  onSubmit(form) {
    form.charges = form.charges.map(charges => ({
      ...charges,
      accountId: this.accountId,
    }))
    form.waiver = this.flag
    form.accountId = this.accountId
    console.log("form value", form.charges);
    
    this.api.chargeDetailsSave(form.charges).subscribe(resp => {
      this.snack.open('Charge Details Saved!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
      console.log("resp from backend", resp)
      this.ls.setItem('enrichmentStage', true);
      this.ls.setItem('ENRICHMENT_CHARGES_DONE', true);
      this.accountService.refresh.next();
      this.router.navigate([`/home/application/assessment/list/${this.accountId}`]);
    }, err => {
      this.snack.open('Server Error!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    })

  }
  checkboxChanged(event) {
    console.log(event.checked);
    this.flag = event.checked;
  }
  check(accountType) {

    if (accountType) {

      console.log(this.snack.open)
      this.snack.open('Account type is null!', 'Error', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    }
  }
  _keyPress(event: any) {
    const pattern = /[a-zA-Z]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }

  onKeyPress(event: any) {
    const number = /[0-9\+\-\ ]/;
    let inputCharacter = String.fromCharCode(event.charCode);
    if (event.keyCode != 8 && !number.test(inputCharacter)) {
      event.preventDefault();
      console.log("event:", event)
    }
  }
}
