import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import {
  ApexNonAxisChartSeries,
  ApexPlotOptions,
  ApexChart,
  ChartComponent,
  ApexAxisChartSeries,
  ApexFill,
  ApexStroke,
  ApexYAxis,
  ApexTooltip,
  ApexMarkers,
  ApexXAxis

} from "ng-apexcharts";
export type ChartOptions = {
  series: ApexNonAxisChartSeries;
  chart: ApexChart;
  labels: string[];
  plotOptions: ApexPlotOptions;
  series1: ApexAxisChartSeries;
  
  xaxis: ApexXAxis;
  yaxis: ApexYAxis | ApexYAxis[];

  stroke: ApexStroke;
  markers: ApexMarkers;
  fill: ApexFill;
  tooltip: ApexTooltip;

};


export type ChartOptions1 = {
  series1: ApexNonAxisChartSeries;
  chart1: ApexChart;
  labels1: string[];
  plotOptions1: ApexPlotOptions;
  
  
  xaxis1: ApexXAxis;
  yaxis1: ApexYAxis | ApexYAxis[];

  stroke1: ApexStroke;
  markers1: ApexMarkers;
  fill1: ApexFill;
  tooltip1: ApexTooltip;

};
@Component({
  selector: 'app-financial-details-summary',
  templateUrl: './financial-details-summary.component.html',
  styleUrls: ['./financial-details-summary.component.scss']
})
export class FinancialDetailsSummaryComponent implements OnInit {
  
  public chartOptions: Partial<ChartOptions> | any;
  public chartOptions1: Partial<ChartOptions1>|any;
  selectedYear: number;
years: number[] = [];
  curdate: string;
  financialdata: any;
  accountId: any;
  income: any; 
  exepse : any;
  accountInfo: any;
  constructor( private router: Router,private api: ApiService, private ls:LocalStoreService, private dataService:DataService,
    public dialogRef: MatDialogRef<FinancialDetailsSummaryComponent>)
     {
    this.selectedYear = new Date().getFullYear();
    this.curdate = new Date().getDay()+'/'+(new Date().getMonth() + 1).toString() + '/' + new Date().getFullYear().toString();
  }
 
  

  ngOnInit(): void {
 
    this.accountId = this.ls.getItem('accountId');
    console.log(this.accountId);
    this.accountInfo = this.ls.getItem('EDIT-CREATEACCCIF');
    if(this.accountInfo){
      console.log(this.accountInfo)
      this.accountId=this.accountInfo.businessProductAccount.accountId
    }
    setTimeout(() => {
      this.getFinancialDetailsSummary(); 
    }, 1);
   
  }
   

  edit(){
    this.dataService.sendNavParam(this.financialdata);
    this.dialogRef.close();
    
    this.router.navigate([`/home/application/limit-entry-stage/financial-details/${this.accountId}`]);
  }
  close(){
    this.dialogRef.close();
    this.router.navigateByUrl('/home/application/limit-entry-stage/list/');
  }
  getFinancialDetailsSummary(){
   
    // this.accountId = this.ls.getItem('accountId');
    console.log("accountId",this.accountId)
    this.api.getFinancialDetails(this.accountId).subscribe(result=>
      {
        console.log(result);
        this.financialdata=result;
        this.income = this.financialdata.applicantTotalIncome;
        this.exepse = this.financialdata.applicantTotalExpense;
        console.log("income",this.income);
            this.chartOptions = {
      series: [48],
      
      chart: {
        
        height: 200,
        type: "radialBar"
      },
      plotOptions: {
        radialBar: {
          hollow: {
            size: "70%"
          }
        }
      },
      labels: ['saved']
    }
    // chart 2
  this.chartOptions1 = {
    series1: [
      {
        name: "Income :"+ this.income,
        type: "line",
        data: [40,55, 50, 70, 65, 68, 50, 45, 35, 30, 38, 40]
      },
     
      {
        name: "Expenses :"+ this.exepse,
        type: "line",
        data: [55, 30, 25, 45, 46, 47, 48, 52, 44, 35, 30]
      }
    ],
    chart1: {
      height: 220,
      type: "line"
    },
    stroke1: {
      curve: "smooth"
    },
    fill1: {
      type: "solid",
      opacity: [0.35, 1]
    },
    labels1: [
      "JAN",
      "",
      "FEB",
      "",
      "MAR",
      "",
      "APR",
      "",
      "MAY ",
      "",
      "JUN"
    ],
    // markers1: {
      // size: 0
    // },
    yaxis1: [
      {
        title: {
          text: ""
        }
      },
       ],
    xaxis1: {
      labels: {
        trim: false
      }
    },
   

}
      })
      
  }
}


