import { MatSnackBar } from '@angular/material/snack-bar';
import { ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatButton } from '@angular/material/button';
import { Router } from '@angular/router';
import { FundingStageService } from 'app/shared/services/funding-stage.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from '../../account.service';
import { UrlService } from '../../url.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { ApiService } from 'app/shared/services/api.service';

@Component({
  selector: 'app-initial-fund',
  templateUrl: './initial-fund.component.html',
  styleUrls: ['./initial-fund.component.scss']
})
export class InitialFundComponent implements OnInit {

  fundStageFormGroup: FormGroup;
  addOrviewdetails: any = '';
  // formDirective: FormGroupDirective
  @ViewChild('submitBtn') submitButton: MatButton;
  actionAddOrEdit: boolean = true;
  actionView: boolean = false;
  accountId: any;
  action: any;
  previousUrl: string = '';
  showEditOption: boolean = false;
  disabletellerTrxnRefNo: boolean = false;
  disableFundByAccount: boolean = false;
  disabletellerTrxnStatus: boolean = false;
  disablechequeNumber: boolean = false;
  fundBy: any;
  accountInfo: any;
  chequeNoLenCheck: boolean=false;

  constructor(private router: Router,
    private formBuilder: FormBuilder,
    private cdr: ChangeDetectorRef,
    private accountService: AccountService,
    private ls: LocalStoreService,
    private fundingStageService: FundingStageService,
    private snack: MatSnackBar,
    private urlService: UrlService,
    private loader: AppLoaderService,
    private apiService: ApiService) { }

  ngOnInit() {
    /* For getting account id ,from local storage*/
    this.accountId = this.ls.getItem('accountId');
    this.accountInfo = this.ls.getItem('EDIT-CREATEACCCIF');
    if(this.accountInfo){
      this.accountId=this.accountInfo.businessProductAccount.accountId
    }
    let isInitialFundScreen: boolean = JSON.parse(this.ls.getItem('FUNDING_INI_DONE'));

    this.urlService.previousUrl$
      .subscribe((previousUrl: string) => {
        this.previousUrl = previousUrl
      });
setTimeout(() => {
  if (this.accountId != null && isInitialFundScreen) {
    this.showEditOption = true;
    this.getFundsByAccountId();
  } else {
    this.showEditOption = false;
    this.buildFundFormGroup();
  }

}, 1);
   
    this.fundStageFormGroup.valueChanges.subscribe(data => {
      console.log('data :: ', data)
      let chno=data.chequeNumber
      if ((+chno) >0) {
        console.log('in if :: ',data.chequeNumber)
        var x = data.chequeNumber;

        console.log(x.toString().length);
        if(x.toString().length<6){
          this.chequeNoLenCheck=true;
        }else{
          this.chequeNoLenCheck=false;
        }
      }
    })
  }

  buildFundFormGroup(data?) {
    this.fundStageFormGroup = this.formBuilder.group({
      fundId: [data ? data.fundId : ''],
      fundBy: [data ? data.fundBy : '', [Validators.required]],
      amount: [data ? data.amount : ''],
      tellerTrxnRefNo: [data ? data.tellerTrxnRefNo : ''],
      fundByAccount: [data ? data.fundByAccount : ''],
      chequeNumber: [data ? data.chequeNumber : '', [Validators.maxLength]],
      branchCode: [data ? data.branchCode : ''],
      tellerTrxnStatus: [data ? data.tellerTrxnStatus : ''],
      createdBy: [data ? data.createdBy : ''],
      createdTime: [data ? data.createdTime : ''],
      modifiedBy: [data ? data.modifiedBy : ''],
      modifiedTime: [data ? data.modifiedTime : ''],
      accountId: [this.accountId != null ? this.accountId : ''],
    });
    if (data?.fundByAccount) {
      this.fetchAccountBalance(data.fundByAccount, true);
    }
  }

  goBack() {
    this.router.navigate([`${this.previousUrl}`]);
  }
  selected(item) {
    console.log(item);
    if (item == "Fund Transfer") {
      this.disableFundByAccount = true;
      this.disabletellerTrxnStatus = true;
      this.disabletellerTrxnRefNo = false;
      this.disablechequeNumber = false;
      if (this.accountInfo != null) {
        this.fundStageFormGroup.get('branchCode').patchValue(this.accountInfo.branchCode);
        this.fundStageFormGroup.get('fundByAccount').patchValue(this.accountInfo.accountId);
      }
      this.fundStageFormGroup.get('chequeNumber').patchValue("");
      this.fundStageFormGroup.get('tellerTrxnRefNo').patchValue("");
      this.fundStageFormGroup.get('amount').patchValue("");
      this.fundStageFormGroup.get('fundByAccount').patchValue("");
    }
    if (item == "Cheque") {
      this.disableFundByAccount = true;
      this.disablechequeNumber = true;
      this.disabletellerTrxnRefNo = false;
      this.disabletellerTrxnStatus = false;
      if (this.accountInfo != null) {
        this.fundStageFormGroup.get('fundByAccount').patchValue(this.accountInfo.accountId);
      }
      this.fundStageFormGroup.get('branchCode').patchValue("");
      this.fundStageFormGroup.get('tellerTrxnRefNo').patchValue("");
      this.fundStageFormGroup.get('amount').patchValue("");
      this.fundStageFormGroup.get('fundByAccount').patchValue("");
    }
    if (item == "Cash") {
      this.disabletellerTrxnRefNo = true;
      this.disableFundByAccount = false;
      this.disablechequeNumber = false;
      this.disabletellerTrxnStatus = false;
      this.fundStageFormGroup.get('branchCode').patchValue("");
      this.fundStageFormGroup.get('chequeNumber').patchValue("");
      this.fundStageFormGroup.get('amount').patchValue("");
      this.fundStageFormGroup.get('fundByAccount').patchValue("");
    }
    this.fundStageFormGroup.get('fundBy').setValue(item);
  }

  onSubmit(fundStageFormGroup) {

    console.log("fund ", fundStageFormGroup);
    /* TO-DO :: HERE WE NEED TO SET ACCOUNT ID FROM ACCOUNT DETAILS SCREEN */
    fundStageFormGroup.accountId = this.accountId;//"SID456";
    this.fundingStageService.saveOrUpdateFundStageDetails(fundStageFormGroup)
      .subscribe(fundResp => {
        console.log("fundResp :: ", fundResp)
        if (fundResp.accountId != null || fundResp.accountId != '' || fundResp.accountId != "") {
          this.fundingStageService.isSubmittedResp(true);
          this.ls.setItem('fundingStage', true);
          this.ls.setItem('FUNDING_INI_DONE', true);
          this.accountService.refresh.next();
          this.router.navigate([`/home/application/approval/list/${this.accountId}`]);
          this.snack.open('Fund Saved!', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
          this.cdr.markForCheck();
        } else {
          this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
          console.log("500 error")
        }
      }, error => {
        console.log("error :: ", error);
        this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      });
  }

  getFundsByAccountId() {
    this.fundingStageService.getFundByAccountId(this.accountId)
      .subscribe((fundResp: any) => {
        if (fundResp != null) {
          console.log(fundResp);
          if (fundResp.fundBy == "Fund Transfer") {
            this.disableFundByAccount = true;
            this.disabletellerTrxnStatus = true;
            this.disabletellerTrxnRefNo = false;
            this.disablechequeNumber = false;
          }
          if (fundResp.fundBy == "Cheque") {
            this.disableFundByAccount = true;
            this.disablechequeNumber = true;
            this.disabletellerTrxnRefNo = false;
            this.disabletellerTrxnStatus = false;
          }
          if (fundResp.fundBy == "Cash") {
            this.disabletellerTrxnRefNo = true;
            this.disableFundByAccount = false;
            this.disablechequeNumber = false;
            this.disabletellerTrxnStatus = false;
          }
          this.buildFundFormGroup(fundResp);
          // console.log(this.fundStageFormGroup);
          // this.snack.open('Fund!', 'OK', {
          //   duration: 4000,
          //   verticalPosition: 'top',
          //   horizontalPosition: 'right'
          // });
          this.cdr.markForCheck();
        } else {
          // this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
          //   duration: 4000,
          //   verticalPosition: 'top',
          //   horizontalPosition: 'right'
          // });

        }
      }, (error: any) => {
        // this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
        //   duration: 4000,
        //   verticalPosition: 'top',
        //   horizontalPosition: 'right'
        // });

      });
  }

  _keyPress(event: any) {
    console.log("PRessed", event);
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

  keyUp(e) {
    console.log("key up Event ::", e);
  }

  validateAmount(e: any) {
    let enteredAmount = e.target.value;
    console.log("amount", enteredAmount);
    if (this.fundStageFormGroup.get('fundBy').value == "Fund Transfer" || this.fundStageFormGroup.get('fundBy').value == "Cheque") {
      if (parseFloat(this.accountInfo.accBalance) < parseFloat(enteredAmount)) {
        this.snack.open(`Amount should not exceed than Account Balance`, 'OK', {
          duration: 2000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      }
    }
  }

  onChangeEvent(accountId: any) {
    console.log("Onchange event", accountId);
    this.fetchAccountBalance(accountId);
  }

  fetchAccountBalance(accountId, isForce?: boolean) {
    this.loader.open();
    this.apiService.fetchAccountBalanceByAccountId(accountId)
      .subscribe(res => {
        console.log("RES", res);
        this.accountInfo = res;
        if (!isForce) {
          if (this.fundStageFormGroup.get('fundBy').value == "Fund Transfer") {
            this.fundStageFormGroup.get('fundByAccount').patchValue(res.accountId);
            this.fundStageFormGroup.get('branchCode').patchValue(res.branchCode);
          }
        }
        this.loader.close();
        this.snack.open('Fetched!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      }, (err: any) => {
        this.loader.close();
        this.snack.open('Invalid AccountId!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      })
  }

}
