import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from '../account.service';

import { MatDialog } from '@angular/material/dialog';

import { rumangoAnimations } from 'app/shared/animations/rumango-animations';
import { ChargeDetailsSummaryComponent } from '../charge-details-summary/charge-details-summary.component';
import { IntrestDetailsSummaryComponent } from '../intrest-details-summary/intrest-details-summary.component';

@Component({
  selector: 'app-application-enrichment',
  templateUrl: './application-enrichment.component.html',
  styleUrls: ['./application-enrichment.component.scss'],
  animations: rumangoAnimations
})
export class ApplicationEnrichmentComponent implements OnInit {

  informations = [
    {
      'id': 1,
      'title': '1. Interest Details',
      'content': "All the Interest details are captured",
      'percentage': 70,
      'status': 'Pending',
      // 'state': 'application/enrichment/interest',
      'avatar': "assets/images/svg/undraw_statistic_chart_38b6.svg"

    }, {
      'id': 2,
      'title': '2. Charge Details',
      'content': "Charge Details are captured",
      'percentage': 100,
      'status': 'Completed',
      //'state': 'application/enrichment/charge',
      'avatar': "assets/images/svg/undraw_Payments_re_77x0.svg"
    },
  ];

  isChargesScreen: boolean = false;
  isInterestScreen: boolean = false;
  accountId: any;
  isEnrichmentStageDone: boolean = false;
  isChargeStageDone: boolean = false;
  intrestFlag: any;
  isFinancialScreen:boolean = false;
  accountInfo: any;

  constructor(public dialog: MatDialog, private router: Router, private ls: LocalStoreService, private cdr: ChangeDetectorRef, private accountService: AccountService) { }

  ngOnInit() {
    this.accountId = this.ls.getItem('accountId');
    this.isEnrichmentStageDone = this.ls.getItem('enrichmentStage');

    this.isChargesScreen = this.ls.getItem('ENRICHMENT_CHARGES_DONE');
    this.isInterestScreen = this.ls.getItem('ENRICHMENT_INTEREST_DONE');
    this.isFinancialScreen = this.ls.getItem('LIMIT_FIN_DONE');
    console.log("this.isInterestScreen", this.isInterestScreen)
    console.log("this.intrestFlag", this.intrestFlag)

    this.accountService.refresh.subscribe((res: any) => {
      this.accountId = this.ls.getItem('accountId');
      // Parent 3
      this.isEnrichmentStageDone = this.ls.getItem('enrichmentStage');
      // Parent 3 childs nav
      this.isChargesScreen = this.ls.getItem('ENRICHMENT_CHARGES_DONE');
      this.isInterestScreen = this.ls.getItem('ENRICHMENT_INTEREST_DONE');
      this.isFinancialScreen = this.ls.getItem('LIMIT_FIN_DONE');

      this.cdr.markForCheck();
    });

    this.accountInfo = this.ls.getItem('EDIT-CREATEACCCIF');
    console.log(this.accountInfo);
    console.log(this.accountId);
    if(this.accountInfo){
    
      this.ls.setItem('limitEntryStage',true);
   
      this.ls.setItem('ENRICHMENT_CHARGES_DONE', true);
      this.ls.setItem('ENRICHMENT_INTEREST_DONE', true);
      this.accountId=this.accountInfo.businessProductAccount.accountId;
    }
    console.log(this.accountId);
   
  }

  goToDetail() {

    const dialogRef = this.dialog.open(ChargeDetailsSummaryComponent, { data: { number: '9656849771', type: 'Mobile Number' }, disableClose: true });
    dialogRef.afterClosed().subscribe((submit) => {


    });


  }
  goToIntrest() {
    const dialogRef = this.dialog.open(IntrestDetailsSummaryComponent, { data: { number: '9656849771', type: 'Mobile Number' }, disableClose: true });
    dialogRef.afterClosed().subscribe((submit) => {
    })

  }
  navigate() {
    this.router.navigate([`/home/application/enrichment/fullSummary`, this.accountId]);
  }
}
