import { Component, Inject, OnInit } from '@angular/core';


import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-secured-draft-summary',
  templateUrl: './secured-draft-summary.component.html',
  styleUrls: ['./secured-draft-summary.component.scss']
})
export class SecuredDraftSummaryComponent implements OnInit {
 
  secureOverdraft:any;
  accountId: any;
  title: string = "Secured Overdraft Details";
  secureoverDraftDetails: any;
 
  constructor( private router: Router,private api: ApiService, private ls: LocalStoreService,public dialogRef: MatDialogRef<SecuredDraftSummaryComponent>,
  private dataService:DataService  ) { }

  ngOnInit(): void {
    this.accountId = this.ls.getItem('accountId');
    this.getSecuredoverdraft();
  }
  edit(){
    
    this.dataService.sendNavParam(this.secureOverdraft.securedOverDrafts); 
    this.dialogRef.close();
    console.log("accountId in edit:",this.accountId)
    this.router.navigateByUrl('/home/application/limit-entry-stage/secured-draft/'+this.accountId);
  }
  close(){
    this.dialogRef.close();
    this.router.navigateByUrl('/home/application/limit-entry-stage/list/');
  }
  getSecuredoverdraft(){
    
    this.api.securedOverdraftSummary(this.accountId).subscribe(result=>{
          this.secureOverdraft=result;
          
          this.secureoverDraftDetails =  this.secureOverdraft.securedOverDrafts;
          console.log(this.accountId)
          console.log("data::",this.secureOverdraft);

      })
  }
}
