import { Routes } from '@angular/router';
import { ApplicationAssessmentComponent } from './application-assessment/application-assessment.component';
import { QualitativeComponent } from './application-assessment/qualitative/qualitative.component';
import { ReportComponent } from './application-assessment/report/report.component';
