
import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { AppLoaderComponent } from 'app/shared/services/app-loader/app-loader.component';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { FundingStageService } from 'app/shared/services/funding-stage.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';


@Component({
  selector: 'app-funding-stage-summary',
  templateUrl: './funding-stage-summary.component.html',
  styleUrls: ['./funding-stage-summary.component.scss']
})
export class FundingStageSummaryComponent implements OnInit {
  fundStageFormGroup: FormGroup;
  // detailsShowOrViewRef: any;
  accountId: string;
  previousUrl: string = '';
  accountInfo: any;
   constructor(
    private router: Router,
    private formBuilder: FormBuilder,
    private ls: LocalStoreService,
    private fundingStageService: FundingStageService,
    private cdr:ChangeDetectorRef,
    private loader:AppLoaderService
    )
   { }

  ngOnInit(): void {
     /* For getting account id */
    // this.accountId = sessionStorage.getItem('accountId');
    this.accountId = this.ls.getItem('accountId');
    this.accountInfo = this.ls.getItem('EDIT-CREATEACCCIF');
    if(this.accountInfo){
      this.accountId=this.accountInfo.businessProductAccount.accountId
    }
    this.fundStageFormGroup = this.formBuilder.group({
      "fundId": [''],
      "fundBy": [''],
      "accountId": [''],
      "amount": [''],
      "tellerTrxnRefNo": [''],
      "tellerTrxnStatus": [''],
      "fundByAccount": [''],
      "chequeNumber":[''],
      "branchCode":[''],
      "createdBy": [''],
      "createdTime": [''],
      "modifiedBy": [''],
      "modifiedTime": ['']
    })
     
  setTimeout(() => {
    this.viewFundsbyAccountId(this.accountId);
  }, 1);
   
  }

  viewFundsbyAccountId(accountId){
    this.loader.open();
    this.fundingStageService.getFundByAccountId(accountId)
    .subscribe((fundResp:any) =>{
      this.loader.close();
      this.fundStageFormGroup.patchValue(fundResp);
      this.cdr.markForCheck();
    },(err:any) =>{
      this.loader.close();
    });
  }

  goBack() {
    // this.router.navigate([`${this.previousUrl}`]);
    this.router.navigate([`/home/application/funding/list/${this.accountId}`]);
  }

  done(){
    // this.router.navigate([`${this.previousUrl}`]);
    this.router.navigate([`/home/application/funding/list/${this.accountId}`]);
  }
}
