import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { rumangoAnimations } from 'app/shared/animations/rumango-animations';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from '../account.service';
import { ViewApprovalDeatilsComponent } from './view-approval-deatils/view-approval-deatils.component';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-approval-stage',
  templateUrl: './approval-stage.component.html',
  styleUrls: ['./approval-stage.component.scss'],
  animations: rumangoAnimations
})
export class ApprovalStageComponent implements OnInit {
  informations = [
    {
      'id': 1,
      'title': '1. Approval Details',
      'content': "Shows the Approval Details",
      'percentage': 70,
      'status': 'Pending',
      'state': 'application/approval/details',
      'avatar': "assets/images/svg/undraw_approve_qwp7.svg"

    }
  ];


  accountId: any;
  isApprovalStageDone: boolean = false;
  isApprovalDetailsScreen: boolean = false;
  isInitialFundScreen:boolean = false;
  accountInfo: any;

  constructor(private router: Router,
    private dialog: MatDialog, private ls: LocalStoreService, private cdr: ChangeDetectorRef, private accountService: AccountService) { }

  ngOnInit() {
    this.accountId = this.ls.getItem('accountId');
    this.isApprovalStageDone = this.ls.getItem('approvalStage');
    this.isApprovalDetailsScreen = this.ls.getItem('APPROVAL_DETAILS_DONE');
    this.isInitialFundScreen = this.ls.getItem('FUNDING_INI_DONE');

    this.accountService.refresh.subscribe((res: any) => {
      this.accountId = this.ls.getItem('accountId');
      // Parent 6
      this.isApprovalStageDone = this.ls.getItem('approvalStage');
      // Parent 6 childs nav
      this.isApprovalDetailsScreen = this.ls.getItem('APPROVAL_DETAILS_DONE');
      this.isInitialFundScreen = this.ls.getItem('FUNDING_INI_DONE');

      this.cdr.markForCheck();
    });
    this.accountInfo = this.ls.getItem('EDIT-CREATEACCCIF');
    console.log(this.accountInfo);
    // if(this.accountInfo){
    //   this.ls.setItem('approvalStage', true);
    //   this.ls.setItem('APPROVAL_DETAILS_DONE', true);
    //   this.ls.setItem('FUNDING_INI_DONE',true);
    //   this.accountId=this.accountInfo.businessProductAccount.accountId;
    // }
  }

  goToDetail() {
    // console.log();
    const dialogRef = this.dialog.open(ViewApprovalDeatilsComponent, {
      width: '700px',
      height:'437px'
    });
    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }
  
  viewSummary() {
    this.router.navigate([`/home/application/summary/approval/${this.accountId}`]);
  }
  // this.router.navigate([`/home/${item.state}`]);
}


