import { ApiService } from './../../../../shared/services/api.service';
import { Component, OnInit, ViewChildren } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { rumangoAnimations } from 'app/shared/animations/rumango-animations';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from '../../account.service';
import { AccountComponent } from '../../application-entry-stage/account/account.component';
import { EsignPopComponent } from '../esign-pop/esign-pop.component';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';
import { UrlService } from '../../url.service';
import { UploadSignEsignComponent } from '../upload-sign-esign/upload-sign-esign.component';
import { DialogService } from 'app/shared/services/dialogs/dialog.service';


@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss'],
  animations: rumangoAnimations
})
export class ListComponent implements OnInit {
  accountId: any;
  accountdetailsForStatusUpdate: Object;
  previousUrl: string = '';
  show: boolean = false;
  show1: any;
  accountInfo: any;
  selectedFile: any;
  data: any;
  res: Object = { 'enable': false, 'image': null };
  isDone: boolean = false;

  constructor(private dialog: MatDialog,
    private router: Router,
    private ls: LocalStoreService,
    private dialogService: DialogService,
    private accountService: AccountService,
    private apiService: ApplicationEntryStageService,
    private urlService: UrlService,
    private api: ApiService) { }

  ngOnInit(): void {
    this.accountId = this.ls.getItem('accountId');
    this.accountInfo = this.ls.getItem('EDIT-CREATEACCCIF');
    if(this.accountInfo){
      this.accountId=this.accountInfo.businessProductAccount.accountId
    }
    this.urlService.previousUrl$
      .subscribe((previousUrl: string) => {
        this.previousUrl = previousUrl
      });


  }


  openPopUp(data?: any) {
    let response: any;
    if (data === undefined || data === null) {
      response = {
        isSignLater: false,
        screen: 'Create Account'
      };
    }
    if (!this.show) {
      let dialogRef: MatDialogRef<any> = this.dialog.open(UploadSignEsignComponent, {
        width: '720px',
        disableClose: true,
        data: ((data === undefined || data === null) ? response : data)
      })
      dialogRef.afterClosed().subscribe(res => {
        this.show = this.ls.getItem("enable");
        if (res) {
          //  this.show=res;
          setTimeout(() => {
            this.getByAccountId(this.accountId);
          }, 1);
          
          //  this.resetState();
        }
      })
    }
  }

  getByAccountId(accountId: any) {
    this.apiService.getByAccountId(accountId)
      .subscribe(resp => {
        this.accountdetailsForStatusUpdate = resp;
        this.updateAccountStatus(resp);
      });
  }

  updateAccountStatus(accData) {
    accData.status = 'BACKLOG';
    this.apiService.updateAccountStatusService(accData)
      .subscribe(updateResp => {
      });
  }

  goBack() {
    // this.router.navigate([this.previousUrl]);
    this.router.navigate([`/home/application/digital-sign/home/${this.accountId}`]);

  }

  resetState() {
    this.ls.removeItem('appEntryStage');
    this.ls.removeItem('ACC_DONE');
    this.ls.removeItem('ACC_OPEN_DONE');
    this.ls.removeItem('ACC_SERVICE_DONE');
    this.ls.removeItem('ACC_MANDATE_DONE');
    this.ls.removeItem('ACC_NOMINEE_DONE');

    this.ls.removeItem('limitEntryStage');
    this.ls.removeItem('LIMIT_SECURED_DONE');
    this.ls.removeItem('LIMIT_UNSECURED_DONE');
    this.ls.removeItem('LIMIT_FIN_DONE');

    // Parent 3
    this.ls.removeItem('enrichmentStage');
    // Subnave of parent 3
    this.ls.removeItem('ENRICHMENT_CHARGES_DONE');
    this.ls.removeItem('ENRICHMENT_INTEREST_DONE');

    // PArent 4
    this.ls.removeItem('assessmentStage');
    // Subnave of parent 4
    this.ls.removeItem('ASSESSMENT_QA_DONE');
    this.ls.removeItem('ASSESSMENT_REPORT_DONE');
    // PArent 5
    this.ls.removeItem('fundingStage');
    // Subnave of parent 5
    this.ls.removeItem('FUNDING_INI_DONE');

    // Parent 6
    this.ls.removeItem('approvalStage');
    // Parent 6 childs nav
    this.ls.removeItem('APPROVAL_DETAILS_DONE');
    this.ls.removeItem('accountId');
    this.ls.removeItem('eSignStage');
    this.ls.removeItem('PRODUCT_CUSTOMER_ID');
    this.ls.removeItem('accountType');
    this.ls.removeItem('intrestFlag');
    this.ls.removeItem('Status');
    this.ls.removeItem("CIF_NUM_PRIMARY");
    this.ls.removeItem("cId");
    this.ls.removeItem("fundByStatus");
    this.ls.removeItem("overDraftByStatus");
    this.ls.removeItem("EDIT-CREATEACCCIF");
    this.ls.removeItem('signedImage');
    sessionStorage.clear();
    this.accountService.refresh.next();
    this.router.navigate(['/home']);
  }

  onSubmit() {
    let payload = {
      accountId: this.accountId,
      esignLink: "gcom",
      subject: "testing"
    };
    this.api.signInLater(payload)
      .subscribe((data: any) => {
        this.getByAccountId(this.accountId);
        this.ls.setItem('eSignStage', true);
        this.ls.setItem('enable',true);
        this.accountService.refresh.next();
        let response: any = {
          email: data.email,
          customerId: data.customerId,
          subject: data.subject,
          isSignLater: true,
          screen: 'Create Account'
        };
        this.isDone=true;
    this.dialogService.alertForSignLater().subscribe((response) => {
      console.log("response ::", response);                 
    });  
    this.show = this.ls.getItem("enable");
        
      }, (err: any) => {


      })
  }

  onDone(){
    this.isDone=true;
    this.dialogService.alertForFinishButton().subscribe((response) => {
      console.log("response ::", response);                 
    });    

  }

  onUpload(event) {
    let data: any = {};
    this.selectedFile = event.target.files[0];
    if (this.data === undefined || this.data === null) {
      this.data = {
        isSignLater: true,
        screen: 'Create Account'
      };
    }
    this.isDone = false;

    if (this.data.screen !== "NewCustomerOnboarding") {
      data.accountId = this.ls.getItem('accountId');
      this.getByAccountId(data.accountId);
    }
    else {
      let customerId = this.data.customerId;
      this.api.getAccountIdByCusId(customerId).subscribe(res => {
        data.accountId = res;
      })
      // this.res = {enable:true, image:this.signaturePad.toDataURL()};
    }
    data.accountId = 14960;
    data.customerId = this.data.customerId;
    data.documentName = this.data.customerId + "Signature";
    data.documentType = "10"
    data.fileType = "image/jpeg";
    data.fileName = "signature";

    const uploadData = new FormData();
    uploadData.append('data', JSON.stringify(data));
    uploadData.append('file', this.selectedFile);
    uploadData.append('accType', "savings")

    this.api.uploadDocument(uploadData).subscribe((op) => {
      if (op) {
        this.show = true;
        this.res = op;
        data.isDone = true;
        let dialogRef: MatDialogRef<any> = this.dialog.open(EsignPopComponent, {
          width: '720px',
          disableClose: true,
          data: ((data === undefined ||data === null) ? this.data : data)
        })
        dialogRef.afterClosed().subscribe(res => {
          if (res) {
            this.show = this.ls.getItem("enable");
            this.getByAccountId(this.accountId);
          }
        })
      }
    });

    if (this.data.screen !== "NewCustomerOnboarding") {
      this.isDone = true;
      this.ls.setItem('enable', true);
    }
    this.ls.setItem('eSignStage', true);
  }

}
