import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-unsecured-draft-summary',
  templateUrl: './unsecured-draft-summary.component.html',
  styleUrls: ['./unsecured-draft-summary.component.scss']
})
export class UnsecuredDraftSummaryComponent implements OnInit {

  unsecureoverdraftdata: any;
  accountId: any;

  constructor(private router: Router,private api: ApiService, private ls:LocalStoreService,
    public dialogRef: MatDialogRef<UnsecuredDraftSummaryComponent>,
    private dataService:DataService) { }

  ngOnInit(): void {
    this.accountId = this.ls.getItem('accountId');
    this.getUnsecuredOverdraftSummary();
  }
  edit(){
    this.dataService.sendNavParam(this.unsecureoverdraftdata); 
    this.dialogRef.close();
    this.router.navigateByUrl('/home/application/limit-entry-stage/unsecured-draft/'+this.accountId);
  }
  close(){
    this.dialogRef.close();
    this.router.navigateByUrl('/home/application/limit-entry-stage/list/');
  }
  getUnsecuredOverdraftSummary(){
    this.api.UnsecuredOverdraftSummary(this.accountId).subscribe(result=>{
        console.log(result);
        this.unsecureoverdraftdata=result;
      })
  }
}
