import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from '../account.service';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { rumangoAnimations } from 'app/shared/animations/rumango-animations';
import { FinancialDetailsSummaryComponent } from './financial-details-summary/financial-details-summary.component';
import { SecuredDraftSummaryComponent } from './secured-draft-summary/secured-draft-summary.component';
import { UnsecuredDraftSummaryComponent } from './unsecured-draft-summary/unsecured-draft-summary.component';

@Component({
  selector: 'app-limit-entry-stage',
  templateUrl: './limit-entry-stage.component.html',
  styleUrls: ['./limit-entry-stage.component.scss'],
  animations:rumangoAnimations
})
export class LimitEntryStageComponent implements OnInit {
  overDraftByStatus:boolean;

  informations = [
    {
      'id':1,
      'title': '1. Secured Overdraft Details',
      'content':"All the collateral details are captured.",
      'percentage':70,
      'status':'Pending',
      'state': 'application/limit-entry-stage/secured-draft',
      'avatar':"assets/images/svg/undraw_secure_files_re_6vdh.svg",
    


    },{
      'id':2,
      'title': '2. Unsecured Overdraft Details',
      'content':"Unsecured Overdraft Limit Information's are collected.",
      'percentage':100,
      'status':'Completed',
      'state': 'application/limit-entry-stage/unsecured-draft/summary',
      'avatar':"assets/images/svg/undraw_Reviewed_docs_re_9lmr.svg",
      
    },{
      'id':3,
      'title': '3. Financial Details',
      'content':"Financial Details such as Monthly Income, Monthly Expenses, Liabilities are captured. .",
      'percentage':100,
      'status':'Completed',
      'state': 'application/limit-entry-stage/financial-details/summary',
      'avatar':"assets/images/svg/undraw_Finance_re_gnv2.svg",
      
    },
  ];

  isSecuredScreen:boolean = false;
  isUnSecuredScreen:boolean = false;
  isFinancialScreen:boolean = false;
  accountId:any;
  isLimitEntryStageDone:boolean = false;
  isAccountNomineesScreen: boolean = false;
  accountInfo: any;

  constructor(private router:Router,private ls: LocalStoreService,private cdr:ChangeDetectorRef ,private accountService:AccountService,
    public dialog: MatDialog) { }

  ngOnInit(){

    this.accountId = this.ls.getItem('accountId');
    this.accountInfo = this.ls.getItem('EDIT-CREATEACCCIF');
    console.log(this.accountInfo);
    let overdraftStatus = this.ls.getItem('overDraftByStatus') || null;
    console.log("fetch",overdraftStatus);
    this.overDraftByStatus = overdraftStatus != null ? overdraftStatus.isActive : false; 
    console.log(this.overDraftByStatus);
    this.isLimitEntryStageDone = this.ls.getItem('limitEntryStage');
    this.isAccountNomineesScreen = this.ls.getItem('ACC_NOMINEE_DONE');
    this.isSecuredScreen = this.ls.getItem('LIMIT_SECURED_DONE');
    this.isUnSecuredScreen = this.ls.getItem('LIMIT_UNSECURED_DONE');
    this.isFinancialScreen = this.ls.getItem('LIMIT_FIN_DONE');
  
    if(this.accountInfo){
      // this.isAppEntryStageDone = this.ls.getItem('appEntryStage');
      // this.ls.setItem('LIMIT_SECURED_DONE',true);
      // this.ls.setItem('LIMIT_UNSECURED_DONE', true);
      // this.ls.setItem('LIMIT_FIN_DONE', true);
      // this.ls.setItem('appEntryStage', true);
      // this.ls.setItem('ACC_NOMINEE_DONE', true);
    }


    this.accountService.refresh.subscribe((res: any) => {
      this.accountId = this.ls.getItem('accountId');
      // Parent 1
      let overdraftStatus = this.ls.getItem('overDraftByStatus') || null;
      this.overDraftByStatus = overdraftStatus != null ? overdraftStatus.isActive : false; 
      this.isLimitEntryStageDone = this.ls.getItem('limitEntryStage');
      // Parent 1 childs nav
      this.isAccountNomineesScreen = this.ls.getItem('ACC_NOMINEE_DONE');
      this.isSecuredScreen = this.ls.getItem('LIMIT_SECURED_DONE');
      this.isUnSecuredScreen = this.ls.getItem('LIMIT_UNSECURED_DONE');
      this.isFinancialScreen = this.ls.getItem('LIMIT_FIN_DONE');
    
      this.cdr.markForCheck();
    });
    if(this.accountInfo){
      this.accountId=this.accountInfo.businessProductAccount.accountId;
    }
  }

  goToDetail(item){
    console.log(item);
    this.router.navigate([`/home/${item.state}`]);
  }
  
  openDialog(): void {
    let dialogRef = this.dialog.open(SecuredDraftSummaryComponent, {
      width: '800px',
      height:'600px',
      // data: { name: this.name, animal: this.animal }
    });
  }

  openDialog1(): void {
    let dialogRef = this.dialog.open(UnsecuredDraftSummaryComponent, {
      width: '800px',
      height:'300px'
      // data: { name: this.name, animal: this.animal }
    });
  }
  openDialog2(): void {
    let dialogRef = this.dialog.open(FinancialDetailsSummaryComponent, {
      width: '900px',
      height:'550px'
      // data: { name: this.name, animal: this.animal }
    });
  }
  viewSummary(){
    this.router.navigate([`/home/application/limit-entry-stage/view-summary/`, this.accountId]);
  }
}
