import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import { AccountService } from '../../account.service';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { UrlService } from '../../url.service';

@Component({
  selector: 'app-summary-approval',
  templateUrl: './summary-approval.component.html',
  styleUrls: ['./summary-approval.component.scss']
})
export class SummaryApprovalComponent implements OnInit {

  approvalForm: FormGroup;
  accountId: any;
  appsummary: any;
  appArr: any = [];
  previousUrl: string = '';
  constructor(private router: Router,
    private snack: MatSnackBar,private urlService:UrlService,private accountService: AccountService, private formBuilder: FormBuilder, private api: ApiService, private ls: LocalStoreService) { }


  ngOnInit(): void {
    this.accountId = this.ls.getItem('accountId');
    this.getData(this.accountId);
    
    this.urlService.previousUrl$
    .subscribe((previousUrl: string) => {
        this.previousUrl = previousUrl
    });

  }

  getData(accountId) {
    this.api.approvalAccountFetch(accountId).subscribe(resp => {
      console.log("approval details", resp)
      // this.appArr=resp;
      this.appArr = resp;

      console.log(this.appArr);
    }, (err: any) => {
      console.log("err",err);
      this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    })
  }

  goBack() {
    this.router.navigate([`${this.previousUrl}`]);
  }

  onSubmit() {
  
    this.router.navigate(['/home/application/approval/list/:id']);
}
 

}
