import { Component, OnInit } from "@angular/core";
import { FormGroup, FormBuilder, FormArray, Validators } from "@angular/forms";
import { MatSnackBar } from "@angular/material/snack-bar";
import { Router } from "@angular/router";
import { forceFillColumnWidths } from "@swimlane/ngx-datatable";
import { rumangoAnimations } from "app/shared/animations/rumango-animations";
import { ApiService } from "app/shared/services/api.service";
import { LocalStoreService } from "app/shared/services/local-store.service";
import { CustomValidators } from "ngx-custom-validators";
import { AccountService } from "../../account.service";
import { UrlService } from "../../url.service";

@Component({
  selector: "app-interest",
  templateUrl: "./interest.component.html",
  styleUrls: ["./interest.component.scss"],
  animations: rumangoAnimations,
})
export class InterestComponent implements OnInit {
  interestForm: FormGroup;
  accountId: any;
  intrestFlag: Boolean;
  accountType:any;
  previousUrl:string = '';

 
  
  constructor(private accountService: AccountService,private urlService:UrlService,private snack: MatSnackBar, private ls: LocalStoreService, private api: ApiService, private router: Router, private formBuilder: FormBuilder) { }

  ngOnInit(): void {
    this.accountId = this.ls.getItem('accountId');
    this.accountType=this.ls.getItem('accountType');
    console.log(this.accountType)
    this.api.getFlexcubeDetails("intrestDetails",  this.accountType).subscribe(response => {
    this.onChangeInterest(response.length);
    console.log(response)
    this.in.patchValue(response)
    })

  
    this.interestForm = this.formBuilder.group({
      interest: new FormArray([]),
    });
    this.onChangeInterest(1);

    this.urlService.previousUrl$
    .subscribe((previousUrl: string) => {
        this.previousUrl = previousUrl
    });

  }
  assign(accountId) {
    // this.accountId = accountId;
  }
  onChangeInterest(e) {

    const numberOfItems = e || 0;
    if (this.in.length < numberOfItems) {
      for (let i = this.in.length; i < numberOfItems; i++) {
        this.in.push(
          this.formBuilder.group({
            intrestType: [""],
            intrestRateApplicable: [""],
            marginIn: ["",[Validators.required,Validators.max(100),Validators.min(0)]],
            effectiveRate: ["",[Validators.required,Validators.max(100),Validators.min(0)]],
            accountId: [""],
            createdDate: [""],
            createdBy: [""],
            lastModifiedDate: [""],
            lastModifiedBy: [""],
            id: ['']
          })
        );
      }
    } else {
      for (let i = this.in.length; i >= numberOfItems; i--) {
        this.in.removeAt(i);
      }
    }
  }

  // convenience getters for easy access to form fields
  get f() {
    return this.interestForm.controls;
  }
  get in() {
    return this.f.interest as FormArray;
  }

  goBack() {
    // this.router.navigate([`${this.previousUrl}`]);
    this.router.navigate([`home/application/enrichment/list/${this.accountId}`]);
  }

  onSubmit(form) {
    console.log("form", form.interest);
    form.interest = form.interest.map(interest => ({
      ...interest,
      accountId: this.accountId,
    }))
    console.log("form value", form);
    console.log("form value", this.interestForm.value);

    this.api.intrestDetailsSave(form.interest).subscribe(resp => {
      this.snack.open('Interest Details Saved!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
      console.log("backend response from intrest saving", resp)
      this.ls.setItem('ENRICHMENT_INTEREST_DONE', true);
      this.ls.setItem('intrestFlag', true);
      this.accountService.refresh.next();
      this.router.navigate([`/home/application/enrichment/charge/${this.accountId}`]);
    }, err => {
      this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    })
  }

  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  
  // check(accountType){

  //   if(accountType){

  //     console.log(this.snack.open)
  //     this.snack.open('Account type is null!', 'Error', {
  //       duration: 4000,
  //       verticalPosition: 'top',
  //       horizontalPosition: 'right'
  //     });
  //   }
  // }
}
