import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup } from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-charge-details-summary',
  templateUrl: './charge-details-summary.component.html',
  styleUrls: ['./charge-details-summary.component.scss']
})
export class ChargeDetailsSummaryComponent implements OnInit {

  form1: Boolean;
  form2: Boolean;

  chargeFormIT: FormGroup;
  savingForm: FormGroup;
  accountId: any;
  accountType: any;
  accountInfo: any;

  constructor(private ls: LocalStoreService, private snack: MatSnackBar, private dialogRef: MatDialogRef<ChargeDetailsSummaryComponent>, private api: ApiService, private router: Router, private formBuilder: FormBuilder) { }

  ngOnInit(): void {
    this.accountId = this.ls.getItem('accountId');
    this.accountType = this.ls.getItem('accountType');
    this.accountInfo = this.ls.getItem('EDIT-CREATEACCCIF');
    
    if(this.accountInfo){
      this.accountId=this.accountInfo.businessProductAccount.accountId
    }
    setTimeout(() => {
      this.api.getChargeValues(this.accountId).subscribe(resp => {
        console.log("backend resp", resp)
        this.check(resp === null)
        console.log("backend resp", resp.length);
  
  
        // const filteredResponse = resp.filter(res => res.intrestType === 'in hand');
  
        this.onChangeInterest(resp.length);
        this.in.patchValue(resp);
  
        // const filteredResponseSave = resp.filter(res => res.intrestType === 'Handling Charge');
        // console.log(filteredResponseSave.length)
        // if(filteredResponseSave.length===0){
        //   this.form2=false;
        // }
        // if(filteredResponseSave.length!=0){
        //   this.form2=false;
        // }
        //       console.log(this.form2)
        //       this.onChangeSaving(filteredResponseSave.length);
        //       this.is.patchValue(filteredResponseSave);
        // this.flagValue(filteredResponseSave.length,filteredResponse.length)
  
  
  
      })
    }, 1);
    

    this.chargeFormIT = this.formBuilder.group({
      chargeIT: new FormArray([]),
    });
    // this.savingForm = this.formBuilder.group({
    //   saving: new FormArray([]),
    // });




  }
  flagValue(form2, form1) {
    if (form1 > 0) {
      this.form1 = true;
    }
    if (form1 == 0) {
      this.form1 = false;
    }
    if (form2 > 0) {
      this.form2 = true;
    }
    if (form2 == 0) {
      this.form2 = false;
    }
    console.log(this.form1, this.form2)
  }

  onChangeInterest(e) {
    const numberOfItems = e || 0;
    if (this.in.length < numberOfItems) {
      for (let i = this.in.length; i < numberOfItems; i++) {
        this.in.push(
          this.formBuilder.group({
            intrestType: [''],
            amount: [''],
            waiver: [false],
            accountId: [''],
            createdDate: [''],
            craetedBy: [''],
            lastModifiedDate: [''],
            lastModifiedBy: ['']
          })
        );
      }
    } else {
      for (let i = this.in.length; i >= numberOfItems; i--) {
        this.in.removeAt(i);
      }
    }
  }
  // onChangeSaving(e) {
  //   const numberOfItems = e || 0;
  //   if (this.is.length < numberOfItems) {
  //     for (let i = this.is.length; i < numberOfItems; i++) {
  //       this.is.push(
  //         this.formBuilder.group({

  //           intrestType: [''],
  //           amount: ['123456'],
  //           waiver: [false],
  //           accountId:[''],
  //           createdDate:[''],
  //           craetedBy:[''],
  //           lastModifiedDate:[''],
  //           lastModifiedBy:['']
  //         })
  //       );
  //     }
  //   } else {
  //     for (let i = this.is.length; i >= numberOfItems; i--) {
  //       this.is.removeAt(i);
  //     }
  //   }
  // }

  // convenience getters for easy access to form fields
  get f() {
    return this.chargeFormIT.controls;
  }
  get in() {
    return this.f.chargeIT as FormArray;
  }
  // get fs() {
  //   return this.savingForm.controls;
  // }
  // get is() {
  //   return this.fs.saving as FormArray;
  // }


  goBack() {
    this.router.navigate(["/home/application/enrichment/list"]);
  }

  onSubmit(form) {
    console.log("form", form.interest);
    console.log("form value", form);
    console.log("form value", this.chargeFormIT.value);
    this.api.intrestDetailsSave(form.interest).subscribe(resp => {
      console.log("backend response from intrest saving", resp)
    }

    )
    this.router.navigate(['/home/application/enrichment/charge']);
  }
  edit() {
    this.dialogRef.close();
    this.api.setchargeFlag({
      chargeflag: true
    })
    this.router.navigate([`/home/application/enrichment/charge/${this.accountId}`]);
  }

  check(accountType) {
    if (accountType) {
      console.log(this.snack.open)
      this.snack.open('charge detail is empty', 'Error', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    }
  }
 
  _keyPress(event: any) {
    const pattern = /[a-zA-Z]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }
  
}

